	const [color, setColor] = useState('#d0e25b');
	const [colorArr, setColorArr] = useState([]);
	const [cvModal, setCvModal] = useState(null);
	const [details, setDetails] = useState([]);
	const [slicedSpecData, setSlicedSpecData] = useState([]);
	const [IsSliced, setIsSliced] = useState(false);
	const [productNo, setproductNo] = useState(0);
	const [varients, setVarients] = useState([]);
	const [Variant, setVariant] = useState(0);
	const [show, setShow] = useState(false);
	const [interest, setInterest] = useState(9.5);
	const [tenure, setTenure] = useState(60);
	const [emi, setEmi] = useState(0);
	const [loan, setLoan] = useState(0);
	const [exRoomPrice, setExRoomPrice] = useState(0);
	const [downPament, setDownPament] = useState(100000);
	const [totalPayAmount, setTotalPayAmount] = useState(0);
	const [chgloanAmount, setChgloanAmount] = useState(0);
	const [wishList, setWishList] = useState(false);
	const [deleteWishListId, setDeleteWishList] = useState({});
	const { var_id } = route.params;

	useEffect(() => {
		carDetails(var_id);
		getVarients(route.params.item_id);
	}, []);

	useEffect(() => {
		calculateLoan();
	}, [downPament, loan, tenure, interest, exRoomPrice, chgloanAmount]);

	const calculateLoan = () => {
		let ConvertedInterest = interest / 100;
		let loanAmount = exRoomPrice - downPament;
		setLoan(loanAmount);
		let newDownPay = exRoomPrice - loanAmount;
		setDownPament(newDownPay);
		let totalInterest = (ConvertedInterest / 12) * tenure;
		let loanAmtWithInt = loanAmount * totalInterest + loanAmount;
		let emiAmount = loanAmtWithInt / tenure;
		setTotalPayAmount(loanAmtWithInt);
		setEmi(emiAmount);
	};

	const carDetails = async (var_id) => {
		const response = await ApiHandler.getDetails('/' + var_id);
		setDetails(response.data);
		carColors(response.data[0].title);
		setSlicedSpecData(response.data[0].spec.slice(0, 8));
		setIsSliced(true);
		setExRoomPrice(response.data[0].price);

		// setLoan(exRoomPrice - downPament);
	};

	const getVarients = async (id) => {
		const response = await ApiHandler.getVarientsList('/' + id);
		setVarients(response.data);
	};

