const InsuranceScreen = ({ navigation }) => {
	return (
		<ScreenWrapper>
			<Header />
			<SearchBar navigation={navigation}/>
			<View>
				<Heading text={'Select Your Model'} />
				<FlatList
					data={data}
					keyExtractor={(k) => k.id}
					numColumns={3}
					renderItem={({ item }) => (
						<TouchableOpacity
							style={{
								alignItems: 'center',
								justifyContent: 'space-between',
								flex: 1,
								margin: 5,
								borderRadius: 12,
								shadowColor: 'gray',
								shadowOpacity: 0.26,
								shadowOffset: { width: 0, height: 2 },
								shadowRadius: 10,
								elevation: 3,
							}}
							onPress={() => {
								navigation.navigate('insurance');
							}}
						>
							<Image
								source={item.img}
								style={{ width: 80, height: 60, resizeMode: 'contain' }}
							/>
							<Text
								style={{
									padding: 5,
									fontSize: 12,
									textAlign: 'center',
									fontWeight: '600',
									color: COLORS.FADED_TEXT,
								}}
							>
								{item.text1}
							</Text>
							<Text
								style={{
									padding: 5,
									fontSize: 12,
									textAlign: 'center',
									fontWeight: '600',
									color: COLORS.FADED_TEXT,
								}}
							>
								{item.text2}
							</Text>
							<View
								style={{
									alignSelf: 'center',
									marginBottom: 10,
									alignItems: 'center',
									justifyContent: 'center',
									backgroundColor: 'orange',
									padding: 4,
									borderRadius: 10,
								}}
							>
								<Text
									style={{
										fontSize: 14,
										fontWeight: '600',
										textAlign: 'center',
									}}
								>
									view Details
								</Text>
							</View>
						</TouchableOpacity>
					)}
				/>
			</View>
		</ScreenWrapper>
	);
};

export default InsuranceScreen;

const styles = StyleSheet.create({});
