import React from 'react';
import Carousel from 'react-native-banner-carousel';
import { StyleSheet, Image, View, Dimensions } from 'react-native';

const BannerWidth = Dimensions.get('window').width - 20;
const BannerHeight = 150;
export default class Banner extends React.Component {
	constructor(props) {
		super(props);
	}
	state = {
		images: [],
	};
	componentDidMount() {
		this.getFilteredData();
	}
	getFilteredData = () => {
		const data = this.props.banner.filter((item) => {
			return item.promotion_type === 'image' && item.image !== null;
		});
		this.setState({ images: data });
	};
	// renderPage(image, index) {
	// 	return (
	// 		<View key={index}>
	// 			<Image
	// 				style={{
	// 					width: BannerWidth,
	// 					height: BannerHeight,
	// 					borderRadius: 10,
	// 					marginTop: 10,
	// 					resizeMode: 'contain',
	// 				}}
	// 				source={{ uri: `http://superapi.kalyanicrm.com${image.image}` }}
	// 				// source={{ uri: image }}
	// 			/>
	// 		</View>
	// 	);
	// }

	render() {
		return (
			<View style={styles.container}>
				{this.state.images ? (
					<Carousel
						autoplay
						autoplayTimeout={3000}
						loop
						index={0}
						pageSize={BannerWidth}
						showsPageIndicator={false}
						useNativeDriver={true}
					>
						{this.state.images.map((image, index) => (
							<View key={index}>
								<Image
									style={{
										width: BannerWidth,
										height: BannerHeight,
										borderRadius: 10,
										marginTop: 5,
										resizeMode: 'contain',
									}}
									source={{
										uri: `http://superapi.kalyanicrm.com${image.image}`,
									}}
								/>
							</View>
						))}
					</Carousel>
				) : (
					<></>
				)}
			</View>
		);
	}
}

const styles = StyleSheet.create({
	container: {
		flex: 1,
		backgroundColor: '#fff',
		justifyContent: 'center',
		// backgroundColor: 'red',
		// marginTop: 10,
	},
});
