import React, { useState, useEffect } from 'react';
import {
	Dimensions,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
} from 'react-native';

import SwipeToDelete from '../components/common/SwipeToDelete';

class Wishlist extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	}


	render() {
		return (
			<View style={styles.container}>
				<SwipeToDelete navigation={this.props.navigation} />
			</View>
		);
	}
}

export default Wishlist;

const styles = StyleSheet.create({
	container: {
		backgroundColor: 'white',
		flex: 1,
	},
	switchContainer: {
		flexDirection: 'row',
		justifyContent: 'center',
		marginVertical: 50,
		flexWrap: 'wrap',
	},
	switch: {
		alignItems: 'center',
		borderWidth: 1,
		borderColor: 'black',
		marginVertical: 2,
		paddingVertical: 10,
		width: Dimensions.get('window').width / 3,
	},
});
