import { Image, StyleSheet, Text, View } from 'react-native';
import React from 'react';
import { COLORS } from '../../Theme/Theme';

const Insurance_comp = ({ img, title, decp }) => {
	return (
		<View
			style={{
				padding: 10,
				alignItems: 'center',
				backgroundColor: 'white',
				shadowColor: 'gray',
				shadowOpacity: 0.26,
				shadowOffset: { width: 0, height: 2 },
				shadowRadius: 10,
				borderRadius: 5,
			}}
		>
			<Image
				source={{ uri: img }}
				style={{ width: 70, height: 70 }}
				resizeMode="contain"
			/>
			<Text style={{ color: COLORS.BLACK, fontWeight: '600' }}>{title}</Text>
			<Text style={{ color: COLORS.FADED_TEXT, fontWeight: '500' }}>
				{decp}
			</Text>
		</View>
	);
};

export default Insurance_comp;

const styles = StyleSheet.create({});
