import {
	ScrollView,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
	FlatList,
	Image,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import Loader from '../components/common/Loader';
import ApiHandler from '../api/ApiHandler';
import SearchBar from '../components/common/SearchBar';
import Header from '../components/common/Header';
import {
	BookmarkIcon,
	CalendarDaysIcon,
	ChevronRightIcon,
	CurrencyRupeeIcon,
	FunnelIcon,
	Squares2X2Icon,
	TruckIcon,
} from 'react-native-heroicons/outline';
import { BottomSheet } from 'react-native-btr';
import RangeSlider, { Slider } from 'react-native-range-slider-expo';
import { COLORS } from '../Theme/Theme';
import ScreenWrapper from '../components/common/ScreenWrapper';

const Transmission = [
	{ id: 1, mode: 'Automatic' },
	{ id: 2, mode: 'Manual' },
];
const fuel = [
	{ id: 1, type: 'PETROL' },
	{ id: 2, type: 'DIESEL' },
	{ id: 3, type: 'CNG' },
];

const Used_car = ({ navigation, route }) => {
	const [cardata, setCardata] = useState([]);
	const [carModel, setCarModel] = useState([]);
	const [isloading, loading] = useState(true);
	const [formdata, setFormdata] = useState({});
	const [show, setShow] = useState();
	const [FromYear, setYearValue] = useState(0);
	const [ToYearValue, setToYearValue] = useState(0);
	const [FromValue, setFromValue] = useState(0);
	const [ToValue, setToValue] = useState(0);
	const [priceVaue, setPriceValue] = useState(0);

	useEffect(() => {
		getCarData(route.params.id);
	}, []);

	const getCarData = async (e) => {
		const response = await ApiHandler.getModelList('/' + e);
		setCardata(response.data);
		loading(false);
	};
	const getCarModel = async (e) => {
		const response = await ApiHandler.getVarientsList('/' + e);
		setCarModel(response.data);
	};

	const sendCarInfo = async () => {
		const body = {
			type_of_enq: 'Used Cars',
			title: formdata.brand,
			list: formdata.model,
			detail:
				formdata.transmission +
				'-' +
				formdata.fuel +
				'-' +
				formdata.sYear +
				'-' +
				formdata.eYear,
			mobile: '1234567890',
			email: ' test@gmak.co',
			kmdriven: formdata.fkms + '-' + formdata.tkms,
			price: formdata.price.toString(),
			enquire_at: '2023-01-01',
			scheduled: '2023-01-01',
		};
		const response = await ApiHandler.postUsedCarData(body);
		console.log(response);
		if (response.status === 200) {
			navigation.navigate('thankyou');
		}
	};

	return (
		<>
			{isloading ? (
				<Loader />
			) : (
				<ScrollView
					showsVerticalScrollIndicator={false}
					style={{ backgroundColor: 'white' }}
				>
					<ScreenWrapper>
						<Header />
						<SearchBar navigation={navigation} />

						{/* Brand */}

						<TouchableOpacity
							onPress={() => {
								setShow(1);
							}}
							style={{
								flexDirection: 'row',
								borderBottomWidth: 1,
								alignItems: 'center',
								justifyContent: 'space-between',
								margin: 10,
								paddingVertical: 5,
							}}
						>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									paddingBottom: 10,
								}}
							>
								<BookmarkIcon size={22} color={'#0D4C92'} />
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									{formdata.brand ? formdata.brand : 'Make'}
								</Text>
							</View>
							<ChevronRightIcon size={22} color={'#0D4C92'} />
						</TouchableOpacity>

						<BottomSheet
							onBackButtonPress={() => {
								setShow(null);
							}}
							onBackdropPress={() => {
								setShow(null);
							}}
							visible={show === 1 ? true : false}
						>
							<View style={{ backgroundColor: '#fff', height: 200 }}>
								<View
									style={{
										shadowColor: 'gray',
										textAlign: 'center',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 1 },
										shadowRadius: 5,
										elevation: 1,
										marginBottom: 5,
										width: '100%',
									}}
								>
									<Text style={{ fontSize: 20, fontWeight: '600', padding: 7 }}>
										Please Choose the Brand
									</Text>
								</View>
								<FlatList
									showsVerticalScrollIndicator={false}
									horizontal
									data={cardata}
									renderItem={({ item, index }) => {
										return (
											<TouchableOpacity
												style={{
													alignItems: 'center',
													justifyContent: 'space-between',
													paddingHorizontal: 15,
													paddingVertical: 10,
													flex: 1,
													margin: 5,
													borderRadius: 12,
													shadowColor: 'gray',
													shadowOpacity: 0.26,
													shadowOffset: { width: 0, height: 2 },
													shadowRadius: 10,
													elevation: 3,
												}}
												onPress={() => {
													setShow(null);
													setFormdata({ ...formdata, ['brand']: item.title });
													getCarModel(item.id);
												}}
											>
												<Image
													style={{
														height: 60,
														width: 60,
														resizeMode: 'contain',
													}}
													source=""
												/>
												<Text>{item.title}</Text>
											</TouchableOpacity>
										);
									}}
								/>
							</View>
						</BottomSheet>

						{/* Model */}

						<TouchableOpacity
							onPress={() => {
								setShow(2);
							}}
							style={{
								flexDirection: 'row',
								borderBottomWidth: 1,
								alignItems: 'center',
								justifyContent: 'space-between',
								margin: 10,
								paddingVertical: 5,
							}}
						>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									paddingBottom: 10,
								}}
							>
								<Squares2X2Icon size={22} color={'#0D4C92'} />
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									{formdata.model ? formdata.model : 'Model'}
								</Text>
							</View>
							<ChevronRightIcon size={22} color={'#0D4C92'} />
						</TouchableOpacity>

						<BottomSheet
							onBackButtonPress={() => {
								setShow(null);
							}}
							onBackdropPress={() => {
								setShow(null);
							}}
							visible={show === 2 ? true : false}
						>
							<View style={{ backgroundColor: '#fff', height: 200 }}>
								<View
									style={{
										shadowColor: 'gray',
										textAlign: 'center',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 1 },
										shadowRadius: 5,
										elevation: 1,
										marginBottom: 5,
										width: '100%',
									}}
								>
									<Text style={{ fontSize: 20, fontWeight: '600', padding: 7 }}>
										{formdata.brand
											? 'Please Choose the Model'
											: 'Please Select Brand First'}
									</Text>
								</View>
								<FlatList
									showsVerticalScrollIndicator={false}
									horizontal
									data={carModel}
									renderItem={({ item, index }) => {
										return (
											<TouchableOpacity
												style={{
													alignItems: 'center',
													justifyContent: 'space-between',
													paddingHorizontal: 15,
													paddingVertical: 10,
													flex: 1,
													margin: 5,
													borderRadius: 12,
													shadowColor: 'gray',
													shadowOpacity: 0.26,
													shadowOffset: { width: 0, height: 2 },
													shadowRadius: 10,
													elevation: 3,
												}}
												onPress={() => {
													setShow(null);
													setFormdata({ ...formdata, ['model']: item.title });
												}}
											>
												<Image
													style={{
														height: 60,
														width: 60,
														resizeMode: 'contain',
													}}
													source=""
												/>
												<Text>{item.title}</Text>
											</TouchableOpacity>
										);
									}}
								/>
							</View>
						</BottomSheet>

						{/* Transmission */}

						<TouchableOpacity
							onPress={() => {
								setShow(3);
							}}
							style={{
								flexDirection: 'row',
								borderBottomWidth: 1,
								alignItems: 'center',
								justifyContent: 'space-between',
								margin: 10,
								paddingVertical: 5,
							}}
						>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									paddingBottom: 10,
								}}
							>
								<TruckIcon size={22} color={'#0D4C92'} />
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									{formdata.transmission
										? formdata.transmission
										: 'Transmission'}
								</Text>
							</View>
							<ChevronRightIcon size={22} color={'#0D4C92'} />
						</TouchableOpacity>

						<BottomSheet
							onBackButtonPress={() => {
								setShow(null);
							}}
							onBackdropPress={() => {
								setShow(null);
							}}
							visible={show === 3 ? true : false}
						>
							<View style={{ backgroundColor: '#fff', height: 200 }}>
								<View
									style={{
										shadowColor: 'gray',
										// textAlign: 'center',
										alignItems: 'center',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 1 },
										shadowRadius: 5,
										elevation: 1,
										marginBottom: 5,
										width: '100%',
									}}
								>
									<Text style={{ fontSize: 20, fontWeight: '600', padding: 7 }}>
										Please Select Transamission Mode
									</Text>
								</View>
								<FlatList
									showsVerticalScrollIndicator={false}
									horizontal
									data={Transmission}
									renderItem={({ item, index }) => {
										return (
											<TouchableOpacity
												style={{
													height: 60,
													width: 100,
													paddingHorizontal: 10,
													alignItems: 'center',
													justifyContent: 'space-between',
													flex: 1,
													margin: 5,
													borderRadius: 12,
													shadowColor: 'gray',
													shadowOpacity: 0.26,
													shadowOffset: { width: 0, height: 2 },
													shadowRadius: 10,
													elevation: 3,
												}}
												onPress={() => {
													setShow(null);
													setFormdata({
														...formdata,
														['transmission']: item.mode,
													});
												}}
											>
												<Text
													style={{
														fontSize: 16,
														paddingVertical: 15,
														fontWeight: '600',
													}}
												>
													{item.mode}
												</Text>
											</TouchableOpacity>
										);
									}}
								/>
							</View>
						</BottomSheet>

						{/* fuel */}

						<TouchableOpacity
							onPress={() => {
								setShow(4);
							}}
							style={{
								flexDirection: 'row',
								borderBottomWidth: 1,
								alignItems: 'center',
								justifyContent: 'space-between',
								margin: 10,
								paddingVertical: 5,
							}}
						>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									paddingBottom: 10,
								}}
							>
								<FunnelIcon size={22} color={'#0D4C92'} />
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									{formdata.fuel ? formdata.fuel : 'Fuel'}
								</Text>
							</View>
							<ChevronRightIcon size={22} color={'#0D4C92'} />
						</TouchableOpacity>

						<BottomSheet
							onBackButtonPress={() => {
								setShow(null);
							}}
							onBackdropPress={() => {
								setShow(null);
							}}
							visible={show === 4 ? true : false}
						>
							<View style={{ backgroundColor: '#fff', height: 200 }}>
								<View
									style={{
										shadowColor: 'gray',
										// textAlign: 'center',
										alignItems: 'center',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 1 },
										shadowRadius: 5,
										elevation: 1,
										marginBottom: 5,
										width: '100%',
									}}
								>
									<Text style={{ fontSize: 20, fontWeight: '600', padding: 7 }}>
										Please Select fuel Type
									</Text>
								</View>
								<FlatList
									showsVerticalScrollIndicator={false}
									horizontal
									data={fuel}
									renderItem={({ item, index }) => {
										return (
											<TouchableOpacity
												style={{
													height: 60,
													width: 100,
													paddingHorizontal: 10,
													alignItems: 'center',
													justifyContent: 'space-between',
													flex: 1,
													margin: 5,
													borderRadius: 12,
													shadowColor: 'gray',
													shadowOpacity: 0.26,
													shadowOffset: { width: 0, height: 2 },
													shadowRadius: 10,
													elevation: 3,
												}}
												onPress={() => {
													setShow(null);
													setFormdata({
														...formdata,
														['fuel']: item.type,
													});
												}}
											>
												<Text
													style={{
														fontSize: 16,
														paddingVertical: 15,
														fontWeight: '600',
													}}
												>
													{item.type}
												</Text>
											</TouchableOpacity>
										);
									}}
								/>
							</View>
						</BottomSheet>

						{/* Year */}
						<View>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									marginHorizontal: 10,
								}}
							>
								<CalendarDaysIcon size={22} color={'#0D4C92'} />
								{/* <CurrencyRupeeIcon size={22} color={'#0D4C92'} /> */}
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									Year Model
								</Text>
							</View>
							<RangeSlider
								min={2000}
								max={2022}
								fromValueOnChange={(value) => {
									setFormdata({ ...formdata, ['sYear']: value });
									setYearValue(value);
								}}
								toValueOnChange={(value) => {
									setFormdata({ ...formdata, ['eYear']: value });
									setToYearValue(value);
								}}
								outOfRangeBarColor="#0D4C92"
								showValueLabels={false}
								styleSize="small"
								inRangeBarColor="#c8c8c8"
							/>

							<Text
								style={{
									textAlign: 'center',
									color: COLORS.FADED_TEXT,
									fontWeight: '500',
									fontSize: 10,
								}}
							>
								KMs Driven Between
							</Text>
							<Text
								style={{
									textAlign: 'center',
									color: COLORS.BLACK,
									fontWeight: '700',
									fontSize: 14,
								}}
							>
								{FromYear} Year - {ToYearValue} Year
							</Text>
						</View>
						<View
							style={{
								height: 1,
								width: '85%',
								backgroundColor: 'gray',
								alignSelf: 'center',
								marginVertical: 8,
							}}
						/>
						{/* km Driven */}

						<View>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									marginHorizontal: 10,
								}}
							>
								<CalendarDaysIcon size={22} color={'#0D4C92'} />
								{/* <CurrencyRupeeIcon size={22} color={'#0D4C92'} /> */}
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									KMs Driven
								</Text>
							</View>
							<RangeSlider
								min={0}
								max={20000}
								fromValueOnChange={(value) => {
									setFormdata({ ...formdata, ['fkms']: value });
									setFromValue(value);
								}}
								toValueOnChange={(value) => {
									setFormdata({ ...formdata, ['tkms']: value });
									setToValue(value);
								}}
								outOfRangeBarColor="#0D4C92"
								showValueLabels={false}
								styleSize="small"
								inRangeBarColor="#c8c8c8"
							/>

							<Text
								style={{
									textAlign: 'center',
									color: COLORS.FADED_TEXT,
									fontWeight: '500',
									fontSize: 10,
								}}
							>
								KMs Driven Between
							</Text>
							<Text
								style={{
									textAlign: 'center',
									color: COLORS.BLACK,
									fontWeight: '700',
									fontSize: 14,
								}}
							>
								{FromValue} KM - {ToValue} KM
							</Text>
						</View>
						<View
							style={{
								height: 1,
								width: '85%',
								backgroundColor: 'gray',
								alignSelf: 'center',
								marginVertical: 8,
							}}
						/>
						{/* price */}

						<View>
							<View
								style={{
									flexDirection: 'row',
									alignItems: 'center',
									marginHorizontal: 10,
								}}
							>
								<CurrencyRupeeIcon size={22} color={'#0D4C92'} />
								<Text
									style={{
										color: 'black',
										marginLeft: 8,
										fontSize: 16,
										fontWeight: '600',
									}}
								>
									Wanted Price
								</Text>
							</View>
							<Slider
								min={0}
								max={1000000}
								valueOnChange={(value) => {
									setFormdata({ ...formdata, ['price']: value });
									setPriceValue(value);
								}}
								outOfRangeBarColor="#0D4C92"
								showValueLabels={false}
								styleSize="small"
								inRangeBarColor="#c8c8c8"
							/>
							<Text
								style={{
									textAlign: 'center',
									color: COLORS.FADED_TEXT,
									fontWeight: '500',
									fontSize: 10,
								}}
							>
								Price Under
							</Text>
							<Text
								style={{
									textAlign: 'center',
									color: COLORS.BLACK,
									fontWeight: '700',
									fontSize: 14,
								}}
							>
								₹ {priceVaue} /-
							</Text>
						</View>

						<TouchableOpacity
							style={{
								alignItems: 'center',
								justifyContent: 'center',
								paddingVertical: 10,
								width: '80%',
								borderRadius: 12,
								alignSelf: 'center',
								marginTop: 10,
								backgroundColor: '#0D4C92',
							}}
							onPress={sendCarInfo}
						>
							<Text style={{ fontSize: 16, fontWeight: '600', color: '#fff' }}>
								Submit
							</Text>
						</TouchableOpacity>
					</ScreenWrapper>
				</ScrollView>
			)}
		</>
	);
};

export default Used_car;

const styles = StyleSheet.create({});
