import React, { useState, useEffect } from 'react';
import {
	View,
	Text,
	StyleSheet,
	FlatList,
	TouchableOpacity,
	Image,
	ScrollView,
} from 'react-native';
import ApiHandler from '../api/ApiHandler';
import ServiceItemList from '../components/common/ServiceItemList';

const Service = ({ navigation, route }) => {
	const [data, setData] = useState([]);
	const [activeId, setActiveId] = useState(0);
	const [show, setShow] = useState(false);
	const [details, setDetails] = useState([]);
	const [index, setIndex] = useState(0);
	const [compo, setCompo] = useState(0);

	const id = route.params.id;

	useEffect(() => {
		getDetails(id);
	}, []);

	const getDetails = async (e) => {
		const response = await ApiHandler.getModelList('/' + e);
		console.log('model', response.data);
		setData(response.data);
		setShow(true);
		getVarients(response.data[0].id);
	};

	const getVarients = async (id) => {
		const response = await ApiHandler.getVarientsList('/' + id);
		console.log('varient', response.data);
		setDetails(response.data);
		setActiveId(id);
		setShow(true);
	};

	return (
		<View style={{ flex: 1, backgroundColor: '#fff' }}>
			<View
				style={{
					width: '100%',
					backgroundColor: 'white',
					borderBottomWidth: 0.2,
					paddingBottom: 4,
					borderBottomColor: '#bbb',
				}}
			>
				<FlatList
					showsHorizontalScrollIndicator={false}
					horizontal
					data={data}
					renderItem={({ item, index, compo }) => {
						return (
							<TouchableOpacity
								onPress={() => {
									getVarients(item.id), setIndex(index), setCompo(compo);
								}}
								activeOpacity={0.8}
								style={{ margin: 8 }}
							>
								<Text style={styles.item} key={item.id}>
									{item.title}
								</Text>
							</TouchableOpacity>
						);
					}}
				/>
				{/* <View></View> */}
			</View>
			{details.length > 0 ? (
				<ScrollView showsVerticalScrollIndicator={false} style={{ flex: 1 }}>
					<Image
						source={{
							uri: 'https://storage.googleapis.com/gomechanic_assets/Sub%20Category%20Images/01.jpg',
						}}
						style={{
							height: 165,
							width: '100%',
							resizeMode: 'contain',
							marginLeft: 5,
							marginRight: 5,
							alignSelf: 'center',
							marginTop: 5,
							borderRadius: 20,
						}}
					/>
					<Text
						style={{
							fontSize: 18,
							fontWeight: '700',
							padding: 10,
						}}
					>
						{data[index].name}
					</Text>

					<FlatList
						data={details}
						renderItem={({ item, index }) => {
							return (
								<ServiceItemList
									data={details}
									activeId={activeId}
									index={index}
									item={item}
								/>
							);
						}}
					/>
				</ScrollView>
			) : null}
		</View>
	);
};

const styles = StyleSheet.create({
	item: {
		backgroundColor: 'white',
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 12,
		elevation: 3,
		padding: 10,
		borderRadius: 10,
		alignItems: 'center',
		justifyContent: 'center',
		fontWeight: 600,
		marginTop: 5,
	},
	desc: {
		fontWeight: '700',
		letterSpacing: '.2px',
		color: '#282828',
		fontSize: 25,
		marginBottom: 35,
		marginTop: 35,
	},
	image: {
		width: '100%',
		height: '100%',
		marginTop: 20,
	},

	button: {
		backgroundColor: 'white',
		borderWidth: 1,
		borderColor: '#e73c33',
		position: 'absolute',
		bottom: 0,
		padding: 8,
		borderRadius: 5,
		right: 15,
		paddingHorizontal: 12,
	},
});

export default Service;
