import React from 'react';
import Carousel from 'react-native-banner-carousel';
import { StyleSheet, Image, View, Dimensions } from 'react-native';
import ApiHandler from '../../api/ApiHandler';

const BannerWidth = Dimensions.get('window').width - 10;
const BannerHeight = 100;
export default class BuySellCarousel extends React.Component {
	constructor(props) {
		super(props);
	}
	state = {
		images: [],
	};

	componentDidMount() {
		this.getBannerData();
	}

	// const [banner, setBanner] = useState([]);

	getBannerData = async (e) => {
		const bannerData = await ApiHandler.getBannerData(e);
		// setBanner(bannerData.data);
		// console.log(bannerData.data);

		this.getFilteredData(bannerData.data);
	};

	getFilteredData = (banner) => {
		let data = [];
		banner.map((item) => {
			if (item.promotion_type === 'image' && item.image !== null) {
				data.push(item);
			}
		});
		this.setState({ images: data });
	};
	render() {
		return (
			<View style={styles.container}>
				{this.state.images ? (
					<Carousel
						autoplay
						autoplayTimeout={3000}
						loop
						index={0}
						pageSize={BannerWidth}
						showsPageIndicator={true}
						pageIndicatorContainerStyle={{
							position: 'relative',
							marginTop: 25,
							alignSelf: 'center',
						}}
						activePageIndicatorStyle={{
							backgroundColor: '#0D4C92',
							paddingHorizontal: 5,
						}}
						useNativeDriver={false}
					>
						{this.state.images.map((image, index) => (
							<View key={index} style={{ borderRadius: 15 }}>
								<Image
									style={{
										height: 150,
										borderRadius: 10,
										marginTop: 5,
										resizeMode: 'cover',
									}}
									source={{
										uri: `http://superapi.kalyanicrm.com${image.image}`,
									}}
									// source={{ uri: image }}
								/>
							</View>
						))}
					</Carousel>
				) : (
					<></>
				)}
			</View>
		);
	}
}

const styles = StyleSheet.create({
	container: {
		flex: 1,
		backgroundColor: '#fff',
		justifyContent: 'center',
		alignItems: 'center',
		// marginVertical:20,
		// backgroundColor: 'red',
		// marginTop: 10,
	},
});
