import { useNavigation } from '@react-navigation/native';
import React, { useState, useEffect } from 'react';
import {
	View,
	Text,
	StyleSheet,
	FlatList,
	TouchableOpacity,
	Image,
} from 'react-native';

const ServiceItemList = ({ data, activeId, item, index }) => {
	const navigation = useNavigation();
	return (
		<View>
			<View
				style={{
					alignItems: 'center',
					justifyContent: 'space-between',
					flexDirection: 'row',
					alignContent: 'center',
					justifyContent: 'center',
					flex: 1,
				}}
			>
				<View
					style={{
						width: '97%',
						borderRadius: 10,
						position: 'relative',
						backgroundColor: 'white',
						shadowColor: 'gray',
						shadowOpacity: 0.26,
						shadowOffset: { width: 0, height: 2 },
						shadowRadius: 12,
						elevation: 3,
						alignSelf: 'center',
						alignContent: 'center',
						marginVertical: 10,
						// marginLeft: 6,
					}}
				>
					<TouchableOpacity
						onPress={() =>
							navigation.navigate('ServiceDetails', {
								activeId: activeId,
								secid: data[index].id,
							})
						}
					>
						<View style={styles.componentContainer}>
							<View
								style={{
									flex: 1,
									padding: 10,
									justifyContent: 'space-between',
									width: '100%',
								}}
							>
								<Text style={styles.title}>{item.title}</Text>
								<View
									style={{
										flexDirection: 'row',
										alignItems: 'center',
										justifyContent: 'flex-start',
									}}
								>
									<Text style={styles.data}>{item.description}</Text>
								</View>
								{item.price === 0 ? (
									<></>
								) : (
									<View style={{}}>
										<Text
											style={{
												fontSize: 15,
												fontWeight: '700',
												color: 'green',
											}}
										>
											Price Start From
										</Text>
										<Text style={{ fontSize: 15, fontWeight: '700' }}>
											{item.price}
										</Text>
									</View>
								)}
							</View>
							<View
								style={{
									padding: 15,
									width: 140,
									height: 140,
									alignSelf: 'baseline',
									alignContent: 'center',
									justifyContent: 'center',
								}}
							>
								<Image
									source={{
										uri: 'https://gomechprod.blob.core.windows.net/gm-retail-app/service-new-images/newB.jpg',
									}}
									style={styles.image}
								/>
							</View>
						</View>
					</TouchableOpacity>
				</View>
			</View>
		</View>
	);
};

export default ServiceItemList;

const styles = StyleSheet.create({
	componentContainer: {
		flex: 1,
		flexDirection: 'row',
		alignItems: 'center',
		justifyContent: 'center',
		width: '100%',
	},

	image: {
		width: '100%',
		height: '100%',
		objectFit: 'contain',
		borderRadius: 6,
		marginLeft: -5,
		boxShadow: '0.5px 1px 3px 0 rgb(0 0 0 / 10%)',
		border: '0.5px solid #e6e6e6',
	},
	title: {
		fontSize: 17,
		fontWeight: 700,
		marginBottom: 10,
	},
	data: {
		fontSize: 13,
		fontWeight: 500,
		lineHeight: 25,
		height: 105,
		overflowY: 'hidden',
		fontFamily: 'Font Awesome 5 Free',
	},
});
