import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import React, { useState } from 'react';
import {
	ChevronDoubleDownIcon,
	ChevronDoubleUpIcon,
} from 'react-native-heroicons/solid';
import { COLORS } from '../../Theme/Theme';

const See_more = ({ title, onPress, direction }) => {
	return (
		<TouchableOpacity style={styles.container} onPress={onPress}>
			<Text style={styles.title}>{title}</Text>
			{direction === 'up' ? (
				<ChevronDoubleUpIcon size={15} color={'#0D4C92'} />
			) : (
				<ChevronDoubleDownIcon size={15} color={'#0D4C92'} />
			)}
		</TouchableOpacity>
	);
};

export default See_more;

const styles = StyleSheet.create({
	container: {
		width: '97%',
		alignItems: 'center',
		flexDirection: 'row',
		justifyContent: 'center',
		padding: 6,
		alignSelf: 'center',
		// borderWidth: 0.5,
		borderRadius: 8,
		borderColor: COLORS.FADED_TEXT,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
		marginTop: 10,
	},
	title: {
		fontSize: 12,
		paddingRight: 5,
		fontWeight: '500',
		color: COLORS.FADED_TEXT,
	},
});
