import {
	FlatList,
	Image,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import { COLORS } from '../../Theme/Theme';
import ApiHandler from '../../api/ApiHandler';
import See_more from './See_more';

const Small_items = ({ navigation, data }) => {
	const [itemList, setItemList] = useState([]);
	// console.log(itemList[0].page_navigation);
	// const handel_seeMore = (e) => {
	const handel_seeMore = () => {
		// console.log(e);
		navigation.navigate('Arena', { id: 1 });
	};

	useEffect(() => {
		setItemList(data);
	}, [data]);

	return (
		<>
			<FlatList
				data={itemList}
				numColumns={3}
				keyExtractor={(item) => item.id}
				renderItem={({ item }) => {
					// console.log(item.id);
					return (
						<TouchableOpacity
							style={styles.container}
							onPress={() =>
								navigation.navigate(item.page_navigation, { id: item.id })
							}
						>
							<View
								style={{
									shadowColor: 'gray',
									shadowOpacity: 0.26,
									shadowOffset: { width: 0, height: 3 },
									shadowRadius: 10,
									elevation: 3,
									paddingLeft: 10,
									borderRadius: 12,
									backgroundColor: COLORS.WHITE,
									marginVertical: 10,
									alignSelf: 'center',
									marginLeft: 10,
									marginRight: 10,
									flex: 1,
								}}
							>
								<Image source={item.images[0]} style={styles.img} />
							</View>
							<Text style={styles.title}>{item.name}</Text>
						</TouchableOpacity>
					);
				}}
			/>
			<See_more
				title={'see more'}
				// onPress={() => handel_seeMore(itemList[0].page_navigation)}
				onPress={handel_seeMore}
				direction={'down'}
			/>
		</>
	);
};

export default Small_items;

const styles = StyleSheet.create({
	container: {
		height: 100,
		width: 80,
		alignItems: 'center',
		justifyContent: 'space-between',
		flex: 1,
		justifyContent: 'center',
	},
	img: {
		height: 60,
		width: 60,
		borderRadius: 10,
		backgroundColor: COLORS.WHITE,
		elevation: 5,
		resizeMode: 'contain',
	},
	title: { color: COLORS.TEXT, fontWeight: '500', fontSize: 12 },
});
