import {
	Alert,
	Image,
	StyleSheet,
	Text,
	TextInput,
	TouchableOpacity,
	View,
	Animated,
} from 'react-native';
import React, { useState, useEffect, useRef } from 'react';
import ScreenWrapper from '../components/common/ScreenWrapper';
import {
	EyeIcon,
	EyeSlashIcon,
	LockClosedIcon,
	PencilIcon,
} from 'react-native-heroicons/solid';
import ApiHandler from '../api/ApiHandler';
import Toast from '../components/common/Toast';
import AsyncStorage from '@react-native-async-storage/async-storage';

const ConfirmPinScreen = ({ length = 6, onComplete, navigation, route }) => {
	const [newPin, setNewPin] = useState('');
	const [confirmPin, setConfirmPin] = useState('');
	const [showPin, setShowPin] = useState('');
	const [loginPin, setLoginPin] = useState('');

	const [value, setValue] = useState('');
	const [isError, setIsError] = useState(false);
	const [shakeAnimation] = useState(new Animated.Value(0));
	const inputRefs = useRef([]);
	const coninputRefs = useRef([]);

	console.log(loginPin);

	const validatePin = (newPin, confirmPin) => {
		if (confirmPin.length == 6) {
			if (newPin !== confirmPin) {
				// console.log('Password do not match');

				setIsError(true);
			} else {
				setIsError(false);
			}
		}
	};

	const registerUser = async (newPin, confirmPin) => {
		const response = await ApiHandler.registerUser({
			username: route.params.mobile,
			password: newPin,
			email: '',
			first_name: '',
			last_name: '',
		});
		profileCreate(route.params.mobile, response.data.id);

		const loginResponse = await ApiHandler.loginUser({
			phone: route.params.mobile,
			password: newPin,
		});
		// console.log(loginResponse);

		try {
			await AsyncStorage.setItem(
				'userCredential',
				JSON.stringify({
					phone: route.params.mobile,
					password: newPin,
					profile_id: loginResponse.data.profile_id,
				})
			);
		} catch (error) {
			console.log(error);
		}

		navigation.navigate('app'),
			{
				phone: route.params.mobile,
				profile_id: loginResponse.data.profile_id,
			};
	};

	const profileCreate = async (mobile, id) => {
		const response = await ApiHandler.ProfileCreate({
			phone: mobile,
			gender: '',
			year_of_birth: 0,
			lat: '',
			long: '',
			user: id,
		});

		console.log(response);
	};

	useEffect(() => {
		if (isError) {
			Animated.sequence([
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: -10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 0,
					duration: 100,
					useNativeDriver: true,
				}),
			]).start(() => setIsError(false));
		}
	}, [isError, shakeAnimation]);

	const newPinHandleTextChange = (text, index) => {
		const newValues = [...newPin];
		newValues[index] = text;
		setNewPin(newValues.join(''));

		if (text.length === 0 && index > 0) {
			inputRefs.current[index - 1].focus();
			newValues[index - 1] = '';
			setNewPin(newValues.join(''));
		} else if (text.length === 1 && index < length - 1) {
			inputRefs.current[index + 1].focus();
		}
		// _handleoptValid(verifyId, newValues.join(''), phone);
	};

	const cnfPinHandleTextChange = (text, index) => {
		const newValues = [...value];
		newValues[index] = text;
		setValue(newValues.join(''));

		if (text.length === 0 && index > 0) {
			coninputRefs.current[index - 1].focus();
			newValues[index - 1] = '';
			setValue(newValues.join(''));
		} else if (text.length === 1 && index < length - 1) {
			coninputRefs.current[index + 1].focus();
		}
		validatePin(newPin, newValues.join(''));
	};

	console.log(newPin);
	console.log(value);

	const newPinHhandleKeyDown = (event, index) => {
		if (event.nativeEvent.key === 'Backspace' && index > 0) {
			const newValues = [...newPin];
			if (newValues[index] !== '') {
				inputRefs.current[index - 1].focus();
				newValues[index - 1] = '';
				console.log(newValues);
			}
			newValues[index] = '';
			setNewPin(newValues.join(''));
		}
	};

	const cnfPinHhandleKeyDown = (event, index) => {
		if (event.nativeEvent.key === 'Backspace' && index > 0) {
			const newValues = [...value];
			if (newValues[index] !== '') {
				coninputRefs.current[index - 1].focus();
				newValues[index - 1] = '';
				console.log(newValues);
			}
			newValues[index] = '';
			setValue(newValues.join(''));
		}
	};

	const inputs = Array(length).fill(0);

	return (
		<ScreenWrapper>
			<View style={{ flex: 1 }}>
				<Image
					source={require('../../assets/images/pin.png')}
					style={{ width: '100%', height: 170, resizeMode: 'contain' }}
				/>

				<Text
					style={{
						fontSize: 22,
						fontWeight: '600',
						color: '#000',
						paddingVertical: 5,
						alignSelf: 'center',
					}}
				>
					Create Your 6 Digit Login Pin
				</Text>

				<View style={{ flex: 1, position: 'relative' }}>
					<View>
						{isError ? (
							<Animated.View
								style={[
									styles.errorText,
									{ transform: [{ translateX: shakeAnimation }] },
								]}
							>
								<View style={styles.container}>
									{inputs.map((_, index) => (
										<TextInput
											key={index}
											value={newPin[index] || ''}
											onChangeText={(text) =>
												newPinHandleTextChange(text, index)
											}
											maxLength={1}
											style={[styles.input, isError && styles.inputError]}
											keyboardType="number-pad"
											ref={(ref) => {
												inputRefs.current[index] = ref;
											}}
											onSubmitEditing={() => {
												if (index === length - 1) {
													inputRefs.current[index].blur();
												}
											}}
											onKeyPress={(event) => newPinHhandleKeyDown(event, index)}
										/>
									))}
								</View>
							</Animated.View>
						) : (
							<View style={styles.container}>
								{inputs.map((_, index) => (
									<TextInput
										key={index}
										value={newPin[index] || ''}
										onChangeText={(text) => newPinHandleTextChange(text, index)}
										maxLength={1}
										style={[styles.input, isError && styles.inputError]}
										keyboardType="numeric"
										ref={(ref) => {
											inputRefs.current[index] = ref;
										}}
										onSubmitEditing={() => {
											if (index === length - 1) {
												inputRefs.current[index].blur();
											}
										}}
										onKeyPress={(event) => newPinHhandleKeyDown(event, index)}
									/>
								))}
							</View>
						)}
					</View>

					<View>
						{isError ? (
							<Animated.View
								style={[
									styles.errorText,
									{ transform: [{ translateX: shakeAnimation }] },
								]}
							>
								<View style={styles.container}>
									{inputs.map((_, index) => (
										<TextInput
											key={index}
											value={value[index] || ''}
											onChangeText={(text) =>
												cnfPinHandleTextChange(text, index)
											}
											maxLength={1}
											style={[styles.input, isError && styles.inputError]}
											keyboardType="number-pad"
											ref={(ref) => {
												coninputRefs.current[index] = ref;
											}}
											onSubmitEditing={() => {
												if (index === length - 1) {
													coninputRefs.current[index].blur();
												}
											}}
											onKeyPress={(event) => cnfPinHhandleKeyDown(event, index)}
										/>
									))}
								</View>
							</Animated.View>
						) : (
							<View style={styles.container}>
								{inputs.map((_, index) => (
									<TextInput
										key={index}
										value={value[index] || ''}
										onChangeText={(text) => cnfPinHandleTextChange(text, index)}
										maxLength={1}
										style={[styles.input, isError && styles.inputError]}
										keyboardType="numeric"
										ref={(ref) => {
											coninputRefs.current[index] = ref;
										}}
										onSubmitEditing={() => {
											if (index === length - 1) {
												coninputRefs.current[index].blur();
											}
										}}
										onKeyPress={(event) => cnfPinHhandleKeyDown(event, index)}
									/>
								))}
							</View>
						)}
					</View>
					{isError ? (
						<Toast message={'warning'} toast={'password did not match !'} />
					) : null}
					<TouchableOpacity
						style={{
							width: '85%',
							alignItems: 'center',
							backgroundColor: '#F74300',
							justifyContent: 'center',
							padding: 13,
							borderRadius: 12,
							alignSelf: 'center',
							position: 'absolute',
							bottom: 8,
						}}
						onPress={() => {
							// navigation.navigate('Registered User'), { phone: route.params.phone };
							// registerUser();
							registerUser(newPin, value);
						}}
					>
						<Text style={{ fontSize: 15, fontWeight: '600', color: 'white' }}>
							Submit
						</Text>
					</TouchableOpacity>
				</View>
			</View>
		</ScreenWrapper>
	);
};

export default ConfirmPinScreen;

const styles = StyleSheet.create({
	container: {
		flexDirection: 'row',
		justifyContent: 'space-between',
		width: '80%',
		alignSelf: 'center',
		marginTop: 20,
	},
	input: {
		height: 40,
		width: 40,
		borderWidth: 1,
		borderRadius: 5,
		textAlign: 'center',
		borderColor: 'lightgray',
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	inputError: {
		borderColor: 'red',
	},
	errorText: {
		color: 'red',
		fontSize: 16,
		marginTop: 8,
	},
});
