import React, { useState, useEffect, useRef } from 'react';
import {
	StyleSheet,
	Text,
	TouchableOpacity,
	TouchableHighlight,
	View,
	Image,
	ScrollView,
} from 'react-native';
import ApiHandler from '../../api/ApiHandler';
import ScreenWrapper from './ScreenWrapper';
import Header from './Header';
import Loader from './Loader';
import { SwipeListView } from 'react-native-swipe-list-view';
import { getComaPrice } from '../util/util';
import { COLORS } from '../../Theme/Theme';
import {
	BoltIcon,
	ShoppingBagIcon,
	TrashIcon,
} from 'react-native-heroicons/solid';

export default function SwipeToDelete({ navigation }) {
	const [listData, setListData] = useState([]);
	const [isLoaded, setIsLoaded] = useState(false);
	const swipeListRef = useRef(null);

	useEffect(() => {
		const focusHandler = navigation.addListener('focus', () => {
			getWishList(1);
		});
		return focusHandler;
	}, [navigation]);

	useEffect(() => {
		getWishList(1);
	}, []);

	const getWishList = async (id) => {
		const response = await ApiHandler.getWishList(id);
		setListData(response.data);

		setIsLoaded(true);
	};

	const deleteRow = async (id) => {
		const response = await ApiHandler.removeWishList(id);
		if (!response.ok) return console.log(response.data);
		getWishList(1);
	};

	const renderItem = (data) => (
		<TouchableOpacity
			activeOpacity={1}
			// underlayColor="red"
			style={{
				width: '97%',
				height: 150,
				borderRadius: 12,
				position: 'relative',
				marginRight: 10,
				backgroundColor: 'white',
				shadowColor: 'gray',
				shadowOpacity: 0.26,
				shadowOffset: { width: 0, height: 2 },
				shadowRadius: 12,
				elevation: 3,
				margin: 6,
			}}
			onPress={() =>
				navigation.navigate('details', {
					var_id: data.item.item.id,
					item_id: data.item.item.itemlist,
				})
			}
		>
			<View
				style={{
					flexDirection: 'row',
					alignItems: 'center',
					justifyContent: 'flex-start',
				}}
			>
				{/* <View
					style={{
						position: 'absolute',
						top: 0,
						right: 15,
						transform: [{ rotate: '180deg' }],
					}}
				>
					<BookmarkIcon size={20} color={'#F74300'} />
				</View> */}
				<View style={{ width: 130, height: 115 }}>
					<Image source={data.item.item.images[0]} style={styles.img} />
				</View>
				<View style={styles.TextCont}>
					<Text style={styles.imgText}>{data.item.item.title}</Text>
					<Text
						style={{
							fontSize: 10,
							fontWeight: '600',
							color: '#bbb',
							marginTop: 4,
						}}
					>
						({data.item.item.color})
					</Text>
					<Text style={styles.desc}>Ex-showroom Price /-</Text>
					<Text style={styles.price}>
						{getComaPrice(data.item.item.price)} /-
					</Text>
				</View>
			</View>
			<View
				style={{
					flex: 1,
					flexDirection: 'row',
					justifyContent: 'center',
					alignItems: 'center',
					borderColor: '#eee',
					borderTopColor: '#bbb',
					borderTopWidth: 0.5,
					borderTopLeftRadius: 4,
					borderTopRightRadius: 4,
				}}
			>
				<TouchableHighlight
					style={{
						width: '50%',
						height: '100%',
						justifyContent: 'center',
						alignItems: 'center',
					}}
					underlayColor="red"
					onPress={() => deleteRow(data.item.id)}
				>
					<View
						style={{
							flexDirection: 'row',
							alignItems: 'center',
							justifyContent: 'center',
						}}
					>
						<TrashIcon size={17} color={'#bbb'} />
						<Text
							style={{
								fontSize: 12,
								fontWeight: '600',
								paddingLeft: 5,
								color: '#bbb',
							}}
						>
							Remove
						</Text>
					</View>
				</TouchableHighlight>
				<View style={{ height: '100%', width: 1, backgroundColor: '#bbb' }} />
				<TouchableHighlight
					style={{
						width: '50%',
						height: '100%',
						justifyContent: 'center',
						alignItems: 'center',
					}}
					underlayColor="green"
					onPress={() => {
						navigation.navigate('ebookscreen', {
							var_id: data.item.item.id,
							item_id: data.item.item.itemlist,
						});
					}}
				>
					<View
						style={{
							flexDirection: 'row',
							alignItems: 'center',
							justifyContent: 'center',
						}}
					>
						<BoltIcon size={17} color={'#bbb'} />
						<Text
							style={{
								fontSize: 12,
								fontWeight: '600',
								paddingLeft: 5,
								color: '#bbb',
							}}
						>
							E-Book
						</Text>
					</View>
				</TouchableHighlight>
			</View>
		</TouchableOpacity>
	);

	const renderHiddenItem = (data) => (
		<View style={styles.rowBack}>
			<TouchableOpacity
				style={{
					borderRadius: 12,
					position: 'relative',
					marginRight: 10,
					backgroundColor: COLORS.WHITE,
					borderWidth: 0.5,
					borderColor: '#eee',
					alignItems: 'flex-start',
					paddingLeft: 20,
					justifyContent: 'center',
					width: 60,
					position: 'absolute',
					left: 0,
					height: '100%',
				}}
				onPress={() => deleteRow(data.item.id)}
			>
				<TrashIcon size={20} color={'red'} />
			</TouchableOpacity>
			<TouchableOpacity
				style={{
					borderRadius: 12,
					position: 'relative',
					marginRight: 10,
					backgroundColor: COLORS.WHITE,
					borderWidth: 0.5,
					borderColor: '#eee',
					alignItems: 'center',
					justifyContent: 'center',
					width: 60,
					position: 'absolute',
					right: 60,
					height: '100%',
				}}
				onPress={() => deleteRow(data.item.id)}
			>
				<TrashIcon size={20} color={'red'} />
			</TouchableOpacity>
			<TouchableOpacity
				style={{
					borderRadius: 12,
					position: 'relative',
					backgroundColor: COLORS.WHITE,
					borderWidth: 0.5,
					borderColor: '#eee',
					alignItems: 'center',
					justifyContent: 'center',
					width: 60,
					position: 'absolute',
					right: 0,
					height: '100%',
				}}
				onPress={() => {
					// navigation.navigate('details');
				}}
			>
				<ShoppingBagIcon size={20} color={'green'} />
			</TouchableOpacity>
		</View>
	);

	return (
		<ScreenWrapper>
			<Header />
			<View style={{ backgroundColor: '#fff', flex: 1 }}>
				<View style={styles.container}>
					<Text
						style={{
							textAlign: 'center',
							textTransform: 'uppercase',
							fontSize: 18,
							fontWeight: '600',
							paddingVertical: 10,
						}}
					>
						Wishlisted cars
					</Text>
					{isLoaded ? (
						<SwipeListView
							showsVerticalScrollIndicator={false}
							ref={swipeListRef}
							data={listData}
							renderItem={renderItem}
							renderHiddenItem={renderHiddenItem}
							leftOpenValue={75}
							rightOpenValue={-140}
							previewRowKey={'0'}
							previewOpenValue={-40}
							previewOpenDelay={3000}
							// onRowDidOpen={onRowDidOpen}
						/>
					) : (
						<View style={{ marginTop: 20 }}>
							<Loader />
							<Loader />
							<Loader />
						</View>
					)}
				</View>
			</View>
		</ScreenWrapper>
	);
}

const styles = StyleSheet.create({
	container: {
		backgroundColor: 'white',
		// flex: 1,
		height: '100%',
		// marginTop: 20,
	},
	backTextWhite: {
		color: '#FFF',
	},
	rowFront: {
		alignItems: 'center',
		backgroundColor: '#CCC',
		borderBottomColor: 'black',
		borderBottomWidth: 1,
		justifyContent: 'center',
		height: 50,
	},
	rowBack: {
		flexDirection: 'row',
		flex: 1,
		margin: 6,
		borderRadius: 12,
	},
	backRightBtn: {
		alignItems: 'center',
		bottom: 0,
		justifyContent: 'center',
		position: 'absolute',
		top: 0,
		width: 75,
		borderRadius: 12,
		backgroundColor: COLORS.WHITE,
		marginLeft: 5,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	backRightBtnLeft: {
		right: 75,
	},
	backRightBtnRight: {
		right: 0,
	},
	cardContainer: {
		width: 300,
		height: 100,
		flexDirection: 'row',
		borderRadius: 12,
		backgroundColor: COLORS.WHITE,
		marginHorizontal: 5,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	desc: {
		fontSize: 13,
		color: COLORS.FADED_TEXT,
		marginTop: 10,
		color: 'green',
		fontWeight: '500',
	},
	img: {
		width: '100%',
		height: '100%',
		resizeMode: 'contain',
	},
	imgText: { fontSize: 14, fontWeight: '600', color: '#000', paddingRight: 10 },
	TextCont: {
		width: 180,
		paddingLeft: 5,
		alignItems: 'flex-start',
		justifyContent: 'center',
	},
	price: { fontSize: 14, fontWeight: '600' },
	containerWrapper: { paddingVertical: 10 },
});
