import {
	FlatList,
	Modal,
	StyleSheet,
	Text,
	TextInput,
	ToastAndroid,
	TouchableOpacity,
	View,
	ActivityIndicator,
	ScrollView,
	Image,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import {
	ArrowLeftIcon,
	MagnifyingGlassIcon,
	XMarkIcon,
} from 'react-native-heroicons/outline';
import { MicrophoneIcon } from 'react-native-heroicons/solid';
import ApiHandler from '../api/ApiHandler';
import { COLORS } from '../Theme/Theme';
import AsyncStorage from '@react-native-async-storage/async-storage';

const CarSearch = ({ navigation }) => {
	const [carlist, setCarlist] = useState([]);
	const [input, setInput] = useState([]);
	const [userId, setUserId] = useState({});

	console.log(userId.profile_id);

	useEffect(() => {
		_getUserStorage();
		console.log(navigation);
	}, []);

	const goback = () => {
		if (navigation.goBack() != undefined) {
			navigation.goBack();
		} else {
			navigation.navigate('main');
		}
	};

	const _getUserStorage = async () => {
		try {
			const jsonValue = await AsyncStorage.getItem('userCredential');
			if (jsonValue !== null) {
				const myObject = JSON.parse(jsonValue);
				console.log(myObject);
				setUserId(myObject);
			}
		} catch (error) {
			console.log(error);
		}
	};

	const carsearch = async (e) => {
		setCarlist([]);
		if (e.length >= 4) {
			let body = {
				profile_id: 2,
				keyword: e,
			};
			setInput(e);
			const searchedcars = await ApiHandler.searchFilter(body);
			console.log(searchedcars);
			console.log();
			setCarlist(searchedcars.data.items);
		} else {
			setInput(e);
		}
	};
	return (
		<View style={{ backgroundColor: '#fff', flex: 1 }}>
			<View
				style={{
					flexDirection: 'row',
					alignItems: 'center',
					marginTop: 10,
				}}
			>
				<TouchableOpacity
					onPress={() => goback()}
					style={{ flexDirection: 'row', alignItems: 'center' }}
				>
					<ArrowLeftIcon
						size={20}
						color="red"
						style={{ marginHorizontal: 10 }}
					/>
				</TouchableOpacity>
				<View
					style={{
						width: '85%',
						alignSelf: 'center',
						borderRadius: 16,
						paddingHorizontal: 10,
						flexDirection: 'row',
						alignItems: 'center',
						justifyContent: 'space-between',
						// backgroundColor: '#fcfcea',
						borderColor: 'black',
						shadowColor: 'gray',
						shadowOpacity: 0.26,
						shadowOffset: { width: 0, height: 2 },
						shadowRadius: 10,
						elevation: 3,
						padding: 10,
					}}
				>
					<MagnifyingGlassIcon size={25} color="#0D4C92" />
					<TextInput
						placeholderTextColor={'grey'}
						placeholder='Search  "Your Car"'
						style={{
							paddingLeft: 7,
							outlineStyle: 'none',
							flex: 1,
						}}
						onChangeText={(value) => {
							carsearch(value);
						}}
						value={input}
					/>
					<View style={{ flexDirection: 'row', alignItems: 'center' }}>
						<View
							style={{
								height: 30,
								borderWidth: 0.2,
								borderColor: 'black',
								marginHorizontal: 10,
							}}
						/>
						<MicrophoneIcon size={20} color="#0D4C92" />
					</View>
				</View>
			</View>
			{carlist.length ? (
				<View style={{ flex: 1 }}>
					<FlatList
						data={carlist}
						showsVerticalScrollIndicator={false}
						keyExtractor={(item, index) => index}
						renderItem={({ item }) => (
							<TouchableOpacity
								style={{
									flex: 1,
									width: '95%',
									marginVertical: 8,
									alignItems: 'flex-start',
									justifyContent: 'flex-start',
									flexDirection: 'row',
									backgroundColor: COLORS.WHITE,
									marginHorizontal: 8,
								}}
								onPress={() => {
									navigation.navigate(item.page_navigation, {
										var_id: item.id,
										item_id: item.itemlist,
									});
								}}
							>
								<View
									style={{
										width: 50,
										height: 50,
										alignItems: 'center',
										justifyContent: 'center',
										paddingHorizontal: 10,
										borderRadius: '50%',
										position: 'relative',
										shadowColor: 'grey',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 2 },
										shadowRadius: 12,
										elevation: 3,
									}}
								>
									<Image
										source={
											item.images === null
												? require('../../assets/images/Noimage.png')
												: item.images[0]
										}
										style={{
											width: 50,
											height: 50,
											borderRadius: '50%',
											resizeMode: 'contain',
										}}
									/>
								</View>
								<View style={{ width: '80%' }}>
									<Text
										numberOfLines={1}
										style={{
											fontSize: 15,
											fontWeight: '600',
											paddingLeft: 10,
										}}
									>
										{item.subtitle}
									</Text>
									<Text
										numberOfLines={2}
										style={{
											textAlign: 'justify',
											paddingLeft: 10,
											color: COLORS.FADED_TEXT,
											fontSize: 10,
										}}
									>
										{item.description.slice(0.2)}
									</Text>
								</View>
							</TouchableOpacity>
						)}
					/>
				</View>
			) : (
				<View
					style={{ alignItems: 'center', justifyContent: 'center', flex: 1 }}
				>
					<Image
						source={require('../../assets/images/no_record.jpg')}
						style={{ width: 150, height: 150 }}
					/>
					<Text>not yet searched</Text>
				</View>
			)}
		</View>
	);
};

export default CarSearch;

const styles = StyleSheet.create({});

