import React, { useState, useEffect } from 'react';
import { Text, View, Animated, Dimensions, Image } from 'react-native';
import Carousel from 'react-native-banner-carousel';
import { COLORS } from '../../Theme/Theme';
import Loader from './Loader';

const BannerWidth = Dimensions.get('window').width - 20;
const BannerHeight = 150;

const PromotionalLable = ({ banner }) => {
	const [slideAnim] = useState(new Animated.Value(-200));
	const [fadeAnim] = useState(new Animated.Value(0));
	const [textPromotion, setTextPromotion] = useState([]);

	useEffect(() => {
		getFilteredData();

		const fadeAnimation = Animated.timing(fadeAnim, {
			toValue: 1,
			duration: 1000,
			useNativeDriver: true,
		});
		const slideAnimation = Animated.timing(slideAnim, {
			toValue: 0,
			duration: 1200,
			// useNativeDriver: true,
		});
		Animated.parallel([fadeAnimation, slideAnimation]).start();
	}, []);

	const getFilteredData = () => {
		const data = banner.filter((item) => {
			return item.promotion_type === 'text';
		});
		setTextPromotion(data);
	};

	return (
		<>
			{textPromotion.length > 0 ? (
				<Animated.View
					style={{
						opacity: fadeAnim,
						transform: [{ translateX: slideAnim }],
					}}
				>
					<Carousel
						autoplay
						autoplayTimeout={4300}
						loop
						index={0}
						pageSize={BannerWidth}
						showsPageIndicator={false}
					>
						{textPromotion.map((text, index) => (
							<View style={{ padding: 2 }} key={index}>
								<View
									style={{
										width: '100%',
										height: 65,
										backgroundColor: '#d4f8fc',
										padding: 8,
										alignSelf: 'center',
										flexDirection: 'row',
										alignItems: 'center',
										borderRadius: 10,
										shadowOffset: { width: 0, height: 2 },
										shadowColor: 'grey',
										shadowRadius: 10,
										shadowOpacity: 0.26,
										elevation: 5,
									}}
								>
									<Image
										source={{
											uri: `http://superapi.kalyanicrm.com${text.image}`,
										}}
										style={{ height: 70, width: 70 }}
									/>
									<View style={{ flex: 1, width: '90%', padding: 5 }}>
										<Text
											style={{
												fontWeight: '700',
												marginLeft: 10,
												fontSize: 16,
												paddingVertical: 5,
												color: COLORS.TEXT,
											}}
										>
											{text.title}
										</Text>
										<Text
											style={{
												fontWeight: '600',
												marginLeft: 10,
												fontSize: 12,
												color: COLORS.FADED_TEXT,
												flexWrap: 'wrap',
											}}
										>
											{text.description}
										</Text>
									</View>
								</View>
							</View>
						))}
					</Carousel>
				</Animated.View>
			) : (
				<Loader />
			)}
		</>
	);
};

export default PromotionalLable;
