import React from 'react';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import Home from '../screens/Home';
import Models from '../screens/Models';
import Variants from '../screens/Variants';
import Details from '../screens/Details';
import Profile from '../screens/Profile';
import Used_car from '../screens/Used_car';
import Thank_You from '../screens/Thank_You';
import Insurance from '../screens/Insurance';
import InsuranceScreen from '../screens/InsuranceScreen';
import Service from '../screens/Service';
import UsedCarBrand from '../screens/UsedCarBrand';
import Editprofile from '../screens/Editprofile';
import CarSearch from '../screens/CarSearch';
import EbookScreen from '../screens/EbookScreen';
import AuthNavigatior from './AuthNavigatior';
import Login from '../screens/Login';
import ServiceDetails from '../screens/ServiceDetails';

const Stack = createNativeStackNavigator();

const AppNavigator = () => {
	return (
		<Stack.Navigator>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="main"
				component={Home}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="models"
				component={Models}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="All Cars"
				component={Models}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="Arena"
				component={Models}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="Nexa"
				component={Models}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="varients"
				component={Variants}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="details"
				component={Details}
			/>

			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="editProfile"
				component={Editprofile}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="Used Cars"
				component={UsedCarBrand}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="searchCar"
				component={CarSearch}
			/>

			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="ebookscreen"
				component={EbookScreen}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="ServiceDetails"
				component={ServiceDetails}
			/>

			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="thankyou"
				component={Thank_You}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="Insurance"
				component={Insurance}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="Service"
				component={Service}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="insuranceScreen"
				component={InsuranceScreen}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="AuthNav"
				component={Login}
			/>
		</Stack.Navigator>
	);
};

export default AppNavigator;
