import {
	FlatList,
	Image,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
	ActivityIndicator,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import { COLORS } from '../../Theme/Theme';
import { LinkIcon, StarIcon } from 'react-native-heroicons/solid';
import { UserIcon } from 'react-native-heroicons/outline';
import ApiHandler from '../../api/ApiHandler';
import Loader from './Loader';
import ScreenWrapper from './ScreenWrapper';
// import ContentLoader, {
// 	FacebookLoader,
// 	InstagramLoader,
// 	Bullets,
// } from 'react-native-easy-content-loader';
// import Spinner from 'react-native-loading-spinner-overlay';

const CarBrandModel = ({ navigation, route }) => {
	const [currentData, setCurrentData] = useState([]);
	const [isloading, loading] = useState(true);

	const nav = route.params.id;
	const ModelId = route.params.ModelId;
	// console.log(ModelId);

	useEffect(() => {
		// renderData();
		getCarList('/' + nav);
	}, []);

	const getCarList = async (e) => {
		const response = await ApiHandler.getModelList(e);
		setCurrentData(response.data);
		loading(false);
		// console.log(currentData);
	};

	// const renderData = () => {
	// 	if (nav === 25) {
	// 		getCarList('/');
	// 	} else {
	// 		getCarList('/' + nav);
	// 	}
	// };

	const handelVarient = (item) => {
		navigation.navigate(item.page_navigation, {
			id: item.id,
		});
	};

	return (
		<>
			{!isloading ? (
				<View
					style={{ flex: 1, width: '100%', justifyContent: 'space-around' }}
				>
					<FlatList
						data={currentData}
						numColumns={2}
						keyExtractor={(item) => item.id}
						showsVerticalScrollIndicator={false}
						renderItem={({ item }) => {
							return (
								<TouchableOpacity
									onPress={(e) => handelVarient(item)}
									style={{
										flex: 1,
										shadowColor: 'gray',
										shadowOpacity: 0.26,
										shadowOffset: { width: 0, height: 3 },
										shadowRadius: 10,
										elevation: 3,
										paddingLeft: 10,
										borderRadius: 12,
										backgroundColor: COLORS.WHITE,
										marginVertical: 10,
										alignSelf: 'center',
										marginLeft: 10,
										marginRight: 10,
									}}
								>
									<Image
										source={{
											uri: item.images[0],
										}}
										style={{
											width: '100%',
											height: 150,
											resizeMode: 'contain',
										}}
									/>
									<View
										style={{
											padding: 5,
											alignItems: 'center',
											marginBottom: 5,
										}}
									>
										<Text style={{ fontWeight: '500', fontSize: 13 }}>
											{item.title}
										</Text>
									</View>
								</TouchableOpacity>
							);
						}}
					/>
				</View>
			) : (
				<Loader />
			)}
		</>
	);
};

export default CarBrandModel;
