import {
	StyleSheet,
	Text,
	View,
	TouchableOpacity,
	ScrollView,
	Image,
	TextInput,
} from 'react-native';
import React from 'react';
import { icon, PencilIcon } from 'react-native-heroicons/solid';

const Editprofile = () => {
	return (
		<View style={{ backgroundColor: 'white', flex: 1, position: 'relative' }}>
			<View
				style={{
					backgroundColor: '#506BE6',
					paddingBottom: 10,
					borderBottomRightRadius: 15,
					borderBottomLeftRadius: 15,
				}}
			>
				<View style={{ alignSelf: 'flex-end', padding: 10, paddingRight: 15 }}>
					<TouchableOpacity
						style={{
							padding: 5,
							borderRadius: 5,
							width: 90,
							color: 'blue',
							backgroundColor: '#F2F3F4',
						}}
					>
						<Text
							style={{
								color: 'blue',
								fontSize: 10,
								fontWeight: '700',
								paddingVertical: 3,
								paddingHorizontal: 10,
							}}
						>
							Edit Profile
						</Text>
					</TouchableOpacity>
				</View>
				<View
					style={{
						alignSelf: 'center',
						alignContent: 'center',
						position: 'relative',
						marginTop: 4,
					}}
				>
					<PencilIcon
						size={20}
						color="grey"
						style={{
							position: 'absolute',
							top: '5%',
							left: '75%',
							zIndex: 13,
							cursor: 'pointer',
							backgroundColor: 'lightgrey',
							padding: 6,
							borderRadius: 15,
						}}
					/>
					<Image
						source={{
							uri: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRLe5PABjXc17cjIMOibECLM7ppDwMmiDg6Dw&usqp=CAU',
						}}
						style={{
							width: 100,
							height: 100,
							borderRadius: 50,
							alignSelf: 'center',
							marginBottom: 30,
						}}
					/>
				</View>
			</View>
			<View
				style={{
					padding: 20,
				}}
			>
				<View>
					<Text style={styles.inputlabel}>Your Name</Text>
					<TextInput
						style={styles.textinput}
						placeholder="Enter Your Name"
						value="JAYASIMHA M H"
					/>
				</View>
				<View>
					<Text style={{ ...styles.inputlabel, marginTop: 15 }}>
						Your Email
					</Text>
					<TextInput
						style={styles.textinput}
						placeholder="Enter Your Email"
					></TextInput>
				</View>
			</View>
			<TouchableOpacity
				style={{
					backgroundColor: 'rgb(80, 107, 230)',
					height: 35,
					padding: 5,
					marginTop: 20,
					alignItems: 'center',
					width: '100%',
					alignSelf: 'center',
					borderRadius: 5,
					position: 'absolute',
					bottom: 20,
				}}
			>
				<Text
					style={{
						fontSize: 14,
						fontWeight: '600',
						color: '#fff',
					}}
				>
					Save
				</Text>
			</TouchableOpacity>
		</View>
	);
};

export default Editprofile;

const styles = StyleSheet.create({
	inputlabel: {
		fontSize: 14,
		color: 'grey',
		marginBottom: 10,
	},
	textinput: {
		padding: 10,
		shadowColor: '#171717',
		shadowOffset: { width: -1, height: 2 },
		shadowOpacity: 0.2,
		shadowRadius: 3,
		borderRadius: 5,
		fontWeight: '600',
		outlineStyle: 'none',
	},
});
