import { StyleSheet, Text, View } from 'react-native';
import {
	ShieldExclamationIcon,
	XCircleIcon,
} from 'react-native-heroicons/solid';
import React, { useState, useEffect } from 'react';
import { COLORS } from '../../Theme/Theme';

const Toast = ({ toast, message }) => {
	return (
		<>
			{message === 'success' ? (
				<View
					style={{
						width: '80%',
						marginTop: 60,
						alignItems: 'center',
						justifyContent: 'center',
						alignSelf: 'center',
						padding: 10,
						borderRadius: 12,
						backgroundColor: '#cdffcd',
						borderWidth: 0.5,
						borderColor: 'lightgreen',
						flexDirection: 'row',
					}}
				>
					<ShieldCheckIcon size={20} color={'green'} />
					<Text
						style={{
							paddingLeft: 10,
							fontSize: 12,
							fontWeight: '600',
							colo: COLORS.BLACK,
							textAlign: 'center',
						}}
					>
						{toast}
					</Text>
				</View>
			) : null}
			{message === 'warning' ? (
				<View
					style={{
						marginTop: 60,
						alignItems: 'center',
						justifyContent: 'center',

						alignSelf: 'center',
						padding: 10,
						borderRadius: 12,
						backgroundColor: '#FEBA4A',
						borderWidth: 0.5,
						borderColor: 'orange',
						flexDirection: 'row',
					}}
				>
					<ShieldExclamationIcon size={20} color={'orange'} />
					<Text
						style={{
							paddingLeft: 10,
							fontSize: 12,
							fontWeight: '600',
							colo: COLORS.BLACK,
							textAlign: 'center',
						}}
					>
						{toast}
					</Text>
				</View>
			) : null}
			{message === 'danger' ? (
				<View
					style={{
						width: '80%',
						marginTop: 60,
						alignItems: 'center',
						justifyContent: 'center',

						alignSelf: 'center',
						padding: 10,
						borderRadius: 12,
						backgroundColor: '#F25C4E',
						borderWidth: 0.5,
						borderColor: 'lightgreen',
						flexDirection: 'row',
					}}
				>
					<XCircleIcon size={20} color={'red'} />
					<Text
						style={{
							paddingLeft: 10,
							fontSize: 12,
							fontWeight: '600',
							colo: COLORS.BLACK,
							textAlign: 'center',
						}}
					>
						{toast}
					</Text>
				</View>
			) : null}
		</>
	);
};

export default Toast;

const styles = StyleSheet.create({});
