import {
	StyleSheet,
	Text,
	View,
	Image,
	TouchableOpacity,
	FlatList,
} from 'react-native';
import React from 'react';
import {
	StarIcon,
	HeartIcon,
	UserPlusIcon,
} from 'react-native-heroicons/solid';
import { COLORS } from '../../Theme/Theme';

const BigCard = ({ data }) => {
	console.log(data);
	return (
		<FlatList
			data={data}
			keyExtractor={(item) => item.id}
			renderItem={({ item }) => {
				return (
					<TouchableOpacity style={styles.container}>
						<Image source={item.img} style={styles.img} />
						<View style={styles.subCont}>
							<Text style={styles.title}>{item.title}</Text>
							<View style={styles.ratingCont}>
								<Text style={styles.rating}>{item.rating}</Text>
								<StarIcon size={10} color={COLORS.WHITE} />
							</View>
						</View>
						<View style={styles.subCont}>
							<Text style={styles.subText}>{item.subText}</Text>
							<Text style={styles.subText}>{item.desc}</Text>
						</View>
						<TouchableOpacity style={styles.like}>
							<HeartIcon size={20} color={false ? 'red' : 'grey'} />
						</TouchableOpacity>
						<View
							style={{
								width: '90%',
								height: 1,
								backgroundColor: 'gray',
								alignSelf: 'center',
							}}
						/>
						<View
							style={{
								flexDirection: 'row',
								alignItems: 'center',
								paddingHorizontal: 10,
							}}
						>
							<UserPlusIcon size={20} color={false ? 'red' : 'blue'} />
							<Text
								style={{
									padding: 10,
									fontSize: 14,
									fontWeight: '500',
									color: COLORS.TEXT,
								}}
							>
								10,000 cars sold already.
							</Text>
						</View>
					</TouchableOpacity>
				);
			}}
		/>
	);
};

export default BigCard;

const styles = StyleSheet.create({
	container: {
		width: '97%',
		height: 280,
		resizeMode: 'cover',
		borderRadius: 12,
		position: 'relative',
		alignSelf: 'center',
		backgroundColor: COLORS.WHITE,
		marginTop: 15,
		marginBottom: 5,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	hrLine: {
		borderWidth: 0.5,
		backgroundColor: '#ff008',
		width: '90%',
		alignSelf: 'center',
	},
	img: {
		height: '60%',
		width: '100%',
		borderTopLeftRadius: 10,
		borderTopRightRadius: 10,
		// resizeMode: 'contain',
	},
	like: {
		position: 'absolute',
		right: 23,
		top: 12,
		backgroundColor: COLORS.WHITE,
		height: 30,
		width: 30,
		borderRadius: 15,
		alignItems: 'center',
		justifyContent: 'center',
	},
	rating: {
		color: COLORS.WHITE,
		fontSize: 12,
		fontWeight: '600',
	},
	ratingCont: {
		flexDirection: 'row',
		backgroundColor: COLORS.RATING,
		paddingHorizontal: 6,
		paddingVertical: 3,
		borderRadius: 6,
		alignItems: 'center',
		justifyContent: 'space-between',
	},
	subCont: {
		paddingHorizontal: 10,
		paddingTop: 8,
		flexDirection: 'row',
		justifyContent: 'space-between',
		alignItems: 'center',
	},
	subText: {
		paddingBottom: 12,
		fontSize: 13,
		fontWeight: '500',
		color: COLORS.FADED_TEXT,
	},
	title: { fontSize: 16, fontWeight: '600', color: COLORS.TEXT },
});
