import {
	ScrollView,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
	FlatList,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import SearchBar from '../components/common/SearchBar';
import ClickableButtons from '../components/common/ClickableButtons';
import Heading from '../components/common/Heading';
import SmallCards from '../components/common/SmallCards';
import Small_items from '../components/common/Small_items';
import See_more from '../components/common/See_more';
import LargeCard from '../components/common/LargeCard';
import BigCard from '../components/common/BigCard';
import Category from '../components/common/Category';
import PromotionalLable from '../components/common/PromotionLevel';
import Header from '../components/common/Header';
import ScreenWrapper from '../components/common/ScreenWrapper';
import ApiHandler from '../api/ApiHandler';
import Loading from '../components/common/Loader';
import Banner from '../components/common/Banner';
import AsyncStorage from '@react-native-async-storage/async-storage';

const Home = ({ navigation, route }) => {
	const [isloading, setIsLoading] = useState(false);
	const [small_arena_cars, setSmall_arena_cars] = useState([]);
	const [categorydata, setCategorydata] = useState([]);
	const [primaryData, setPrimaryData] = useState([]);
	const [secondaryData, setSecondaryData] = useState([]);
	const [focusCarData, setFocusCarData] = useState([]);
	const [banner, setBanner] = useState([]);

	// console.log(banner );

	useEffect(() => {
		getAlldata();
	}, [isloading]);

	const getAlldata = () => {
		// setIsLoading(false);
		categoryData();
		getItemList(1);
		getPrimaryData({ title: 'Primary' });
		getSecondaryData({ title: 'Secondary' });
		getFocusedCarData({ title: 'Focused' });
		getBannerData();
	};

	const categoryData = async () => {
		const response = await ApiHandler.getCategoiries();
		// let data = [
		// 	// {
		// 	// 	id: 25,
		// 	// 	title: 'All Cars',
		// 	// 	description: 'Test cars in arena',
		// 	// 	image: 'http://hello.loca.lt/media/showcase/images/s4.jpg',
		// 	// },
		// 	...response.data,
		// ];
		setCategorydata(response.data);
		// {
		// 	response.ok ? setCategorydata(data) : null;
		// }
	};

	const getItemList = async (e) => {
		const small_arena_cars = await ApiHandler.getSmallItemData('/' + e);
		setSmall_arena_cars(small_arena_cars.data);
		// setIsLoading(true);
	};
	const getPrimaryData = async (e) => {
		const primaryData = await ApiHandler.getFeaturedData(e);
		setPrimaryData(primaryData.data);
		// setIsLoading(true);
	};
	const getSecondaryData = async (e) => {
		const secondaryData = await ApiHandler.getFeaturedData(e);
		setSecondaryData(secondaryData.data);
		// setIsLoading(true);
	};
	const getFocusedCarData = async (e) => {
		const focusedData = await ApiHandler.getFeaturedData(e);
		setFocusCarData(focusedData.data);
		// setIsLoading(true);
	};
	const getBannerData = async (e) => {
		const bannerData = await ApiHandler.getBannerData(e);
		setBanner(bannerData.data);
		setIsLoading(true);
	};

	return (
		<ScreenWrapper>
			<ScrollView
				showsVerticalScrollIndicator={false}
				style={{ backgroundColor: 'white', flex: 1 }}
			>
				<Header navigation={(e) => navigation(e)} />
				{/* header section */}

				{/* search section */}
				<SearchBar navigation={navigation} />
				{/* category section */}

				{isloading ? (
					<View>
						<Category data={categorydata} navigation={navigation} />
						{/* promotion section */}
						<View>
							<PromotionalLable banner={banner} />
						</View>

						<View style={{ alignItems: 'center' }}>
							<Banner banner={banner} />
						</View>
						<Heading text={'The Perfect Fit for Your Life Style '} />
						<SmallCards data={focusCarData} navigation={navigation} />
						<Heading text={'Cars That Move Your Mind'} />
						<Small_items navigation={navigation} data={small_arena_cars} />
						<Heading text={'Our Wide Range Of Popular Cars '} />
						<SmallCards data={primaryData} navigation={navigation} />
						<Heading text={'Discover the joy of driving'} />
						<LargeCard data={secondaryData} navigation={navigation} />
					</View>
				) : (
					<>
						<Loading />
						<Loading />
						<Loading />
					</>
				)}
			</ScrollView>
		</ScreenWrapper>
	);
};

export default Home;

const styles = StyleSheet.create({});
