import React from 'react';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import Login from '../screens/Login';
import Otp from '../screens/Otp';
// import { BottomNavigation } from 'react-native-paper';
import ConfirmPinScreen from '../screens/ConfirmPinScreen';
import conformForgotPin from '../screens/ConformForgotPin';
import UserPin from '../screens/UserPin';
import AppNavigator from './AppNavigatior';
import BottomNavigation from './BottomNavigation';
import Profile from '../screens/Profile';
import AddressBook from '../components/common/AddressBook';
import AddAddress from '../components/common/AddAddress';
// import bottomSheet from '@gorhom/bottom-sheet/lib/typescript/components/bottomSheet';

const Stack = createNativeStackNavigator();

const AuthNavigatior = () => {
	return (
		<Stack.Navigator>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="profile"
				component={Profile}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="addressbook"
				component={AddressBook}
			/>
			<Stack.Screen
				options={{
					headerShown: false,
					statusBarColor: 'white',
					statusBarStyle: 'dark',
				}}
				name="addaddress"
				component={AddAddress}
			/>
		</Stack.Navigator>
	);
};

export default AuthNavigatior;
