import {
	FlatList,
	Image,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
} from 'react-native';
import React from 'react';
import { HeartIcon, StarIcon } from 'react-native-heroicons/solid';
import { COLORS } from '../../Theme/Theme';
import { getComaPrice } from '../util/util';

const LargeCard = ({ data, navigation }) => {
	return (
		<FlatList
			horizontal
			data={data}
			showsHorizontalScrollIndicator={false}
			keyExtractor={(item, index) => index}
			renderItem={({ item }) => (
				<View style={styles.containerWrapper}>
					<TouchableOpacity
						style={styles.container}
						onPress={() =>
							navigation.navigate(item[0].page_navigation, {
								var_id: item[0].id,
								item_id: item[0].itemlist,
							})
						}
					>
						<Image source={item[0].images[0]} style={styles.img} />
						<View style={styles.subCont}>
							<Text style={styles.title}>{item[0].title}</Text>
						</View>
						<View
							style={{
								paddingBottom: 15,
								paddingLeft: 10,
							}}
						>
							<Text style={styles.desc}>Ex-showroom Price /-</Text>
							<Text style={styles.offer}>{getComaPrice(item[0].price)} /-</Text>
						</View>
					</TouchableOpacity>
				</View>
			)}
		/>
	);
};

export default LargeCard;

const styles = StyleSheet.create({
	container: {
		width: 180,
		height: 210,
		borderRadius: 12,
		position: 'relative',
		marginRight: 10,
		backgroundColor: COLORS.WHITE,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 12,
		elevation: 3,
	},
	desc: { fontSize: 13, color: COLORS.FADED_TEXT, marginTop: 10 },
	offer: { fontSize: 14, fontWeight: '600' },
	containerWrapper: { padding: 0, height: 220 },
	img: {
		width: '100%',
		height: '55%',
		borderTopLeftRadius: 10,
		borderTopRightRadius: 10,
		resizeMode: 'contain',
	},
	like: {
		position: 'absolute',
		right: 13,
		top: 12,
		backgroundColor: COLORS.WHITE,
		height: 30,
		width: 30,
		borderRadius: 15,
		alignItems: 'center',
		justifyContent: 'center',
	},
	offerSection: {
		height: 25,
		position: 'absolute',
		bottom: 0,
		borderBottomLeftRadius: 12,
		borderBottomRightRadius: 12,
		backgroundColor: 'blue',
		width: '100%',
		opacity: 0.4,
	},
	offerText: {
		padding: 4,
		color: 'black',
		position: 'absolute',
		bottom: 0,
		fontSize: 14,
		fontWeight: '600',
		color: 'black',
	},
	rating: {
		color: COLORS.WHITE,
		fontSize: 12,
		fontWeight: '600',
	},
	ratingCont: {
		flexDirection: 'row',
		backgroundColor: COLORS.RATING,
		paddingHorizontal: 6,
		paddingVertical: 3,
		borderRadius: 6,
		alignItems: 'center',
		justifyContent: 'space-between',
	},
	subCont: {
		paddingHorizontal: 10,
		flexDirection: 'row',
		justifyContent: 'space-between',
		alignItems: 'center',
	},
	subText: {
		paddingHorizontal: 10,
		paddingVertical: 2,
		fontSize: 13,
		fontWeight: '500',
		color: COLORS.FADED_TEXT,
	},
	title: { fontSize: 16, fontWeight: '600', color: COLORS.TEXT },
});
