import React, { useState, useEffect } from 'react';
import { FlatList, Text, TouchableOpacity, View } from 'react-native';
import { COLORS } from '../../Theme/Theme';
import ApiHandler from '../../api/ApiHandler';

const Category = ({ navigation, data }) => {
	// console.log('data', data);

	const [category, setCategory] = useState([]);

	const handelClick = (e, id) => {
		navigation.navigate(e.target.textContent, {
			id: id,
			ModelId: id,
		});
	};

	useEffect(() => {
		setCategory(data);
	}, [data]);

	return (
		<>
			{category ? (
				<FlatList
					horizontal
					showsHorizontalScrollIndicator={false}
					data={category}
					keyExtractor={(item) => item.id}
					renderItem={({ item }) => (
						<TouchableOpacity
							style={{ padding: 2 }}
							onPress={(e) => handelClick(e, item.id)}
						>
							<View
								style={{
									padding: 6,
									borderRadius: 10,
									height: 35,
									width: 80,
									marginTop: 10,
									alignItems: 'center',
									alignItems: 'center',
									justifyContent: 'center',
									marginRight: 10,
									marginBottom: 5,
									shadowColor: 'gray',
									shadowOpacity: 0.26,
									shadowOffset: { width: 0, height: 2 },
									shadowRadius: 10,
									elevation: 3,
								}}
							>
								<Text
									style={{
										alignSelf: 'center',
										marginTop: 2,
										color: COLORS.TEXT,
										fontSize: 12,
										fontWeight: '600',
									}}
								>
									{item.title}
								</Text>
							</View>
						</TouchableOpacity>
					)}
				/>
			) : null}
		</>
	);
};

export default Category;
