import {
	FlatList,
	Image,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import { COLORS } from '../../Theme/Theme';
import ApiHandler from '../../api/ApiHandler';
import Loader from './Loader';
import ScreenWrapper from './ScreenWrapper';

const carVariants = ({ navigation, id }) => {
	const [varients, setVarients] = useState([]);
	const [isFatched, setFatched] = useState(true);
	console.log(id);
	useEffect(() => {
		getVarients(id);
	}, []);

	const queryString = window.location.search;
	const urlParams = new URLSearchParams(queryString);
	const modelId = urlParams.get('ModelId');

	const getVarients = async () => {
		const response = await ApiHandler.getVarientsList('/' + id);
		setVarients(response.data);
		setFatched(false);
	};

	const handelVariant = (idx) => {
		navigation.navigate('details', {
			var_id: idx,
			item_id: id,
		});
	};

	return (
		<>
			{isFatched ? (
				<Loader />
			) : (
				<FlatList
					data={varients}
					keyExtractor={(item) => item.id.toString()}
					numColumns={2}
					showsVerticalScrollIndicator={false}
					renderItem={({ item, index }) => {
						return (
							<TouchableOpacity
								style={styles.container}
								onPress={() => {
									handelVariant(item.id);
								}}
							>
								<Image source={{ uri: item.images[0] }} style={styles.img} />
								<Text style={styles.Heading}>{item.title}</Text>
							</TouchableOpacity>
						);
					}}
				/>
			)}
		</>
	);
};

export default carVariants;

const styles = StyleSheet.create({
	container: {
		flex: 1,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 3 },
		shadowRadius: 10,
		elevation: 3,
		paddingLeft: 10,
		borderRadius: 12,
		backgroundColor: COLORS.WHITE,
		marginVertical: 10,
		alignSelf: 'center',
		marginLeft: 10,
		marginRight: 10,
	},
	Heading: {
		fontWeight: '500',
		fontSize: 13,
		textAlign: 'center',
		paddingBottom: 10,
	},
	img: {
		width: '100%',
		height: 150,
		resizeMode: 'contain',
	},
});
