import {
	StyleSheet,
	Text,
	View,
	Image,
	ScrollView,
	Switch,
	TouchableOpacity,
} from 'react-native';

import React, { useEffect, useState } from 'react';

import Buysellcarousel from '../components/common/BuySellCarousel';
import Sellcar from '../components/common/SellCar';
import Buycar from '../components/common/BuyCar';
import ScreenWrapper from '../components/common/ScreenWrapper';
import Header from '../components/common/Header';

const UsedCarBrand = () => {
	const [radiobtn, setRadiobtn] = useState('buy');

	return (
		<ScreenWrapper>
			<Header />
			<View style={{ flex: 1, marginTop: 0, backgroundColor: 'white' }}>
				<View
					style={{
						flexDirection: 'row',
						width: 90,
						padding: 3,
						borderRadius: 15,
						backgroundColor: '#0D4C92',
						marginVertical: 10,
						alignSelf: 'flex-end',
						marginHorizontal: 15,
					}}
				>
					<TouchableOpacity
						onPress={() => {
							setRadiobtn('buy');
						}}
					>
						<Text
							style={
								radiobtn === 'buy' ? styles.activeradiobtn : styles.radiobtn
							}
						>
							Buy
						</Text>
					</TouchableOpacity>
					<TouchableOpacity
						onPress={() => {
							setRadiobtn('sell');
						}}
					>
						<Text
							style={
								radiobtn === 'sell' ? styles.activeradiobtn : styles.radiobtn
							}
						>
							Sell
						</Text>
					</TouchableOpacity>
				</View>

				<ScrollView
					showsVerticalScrollIndicator={false}
					style={{ backgroundColor: 'white', padding: 8 }}
				>
					<Buysellcarousel />
					<View style={{ width: '100%' }}>
						{radiobtn === 'buy' ? <Buycar /> : <Sellcar />}
					</View>
				</ScrollView>
			</View>
		</ScreenWrapper>
	);
};

export default UsedCarBrand;

const styles = StyleSheet.create({
	radiobtn: {
		padding: 3,
		color: 'white',
		fontSize: 14,
		paddingHorizontal: 10,
	},
	activeradiobtn: {
		paddingHorizontal: 10,
		color: 'white',
		backgroundColor: 'white',
		borderRadius: 15,
		fontSize: 14,
		color: 'black',
		paddingVertical: 3,
	},
});
