import React, { useState, useEffect, useRef } from 'react';
import {
	View,
	TextInput,
	StyleSheet,
	Animated,
	TouchableOpacity,
	Image,
	Text,
} from 'react-native';

import {
	EyeIcon,
	EyeSlashIcon,
	LockClosedIcon,
	PencilIcon,
} from 'react-native-heroicons/solid';
import ApiHandler from '../api/ApiHandler';
import AsyncStorage from '@react-native-async-storage/async-storage';
import ScreenWrapper from '../components/common/ScreenWrapper';
import { COLORS } from '../Theme/Theme';

const UserPin = ({ length = 6, onComplete, navigation, route }) => {
	const [value, setValue] = useState('');
	const [isError, setIsError] = useState(false);
	const [shakeAnimation] = useState(new Animated.Value(0));
	const inputRefs = useRef([]);

	const [newPin, setNewPin] = useState('');
	const [showPin, setShowPin] = useState('');

	const userLogin = async (mobile, pin) => {
		if (pin.length == 6) {
			const response = await ApiHandler.loginUser({
				phone: mobile,
				password: pin,
			});
			// setprofileId(response.data.profile_id);
			if (response.data.error === 404) {
				setIsError(true);
			} else {
				setIsError(false);
				try {
					await AsyncStorage.setItem(
						'userCredential',
						JSON.stringify({
							phone: route.params.mobile,
							password: newPin,
							profile_id: response.data.profile_id,
						})
					);
				} catch (error) {
					console.log(error);
				}

				navigation.navigate('app', {
					phone: mobile,
					profile_id: response.data.profile_id,
				});
			}
		}
	};

	useEffect(() => {
		if (isError) {
			Animated.sequence([
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: -10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 0,
					duration: 100,
					useNativeDriver: true,
				}),
			]).start(() => setIsError(false));
		}
	}, [isError, shakeAnimation]);

	const handleTextChange = (text, index) => {
		const newValues = [...value];
		newValues[index] = text;
		setValue(newValues.join(''));

		if (text.length === 0 && index > 0) {
			inputRefs.current[index - 1].focus();
			newValues[index - 1] = '';
			setValue(newValues.join(''));
		} else if (text.length === 1 && index < length - 1) {
			inputRefs.current[index + 1].focus();
		}
		userLogin(route.params.mobile, newValues.join(''));
	};

	const handleKeyDown = (event, index) => {
		if (event.nativeEvent.key === 'Backspace' && index > 0) {
			const newValues = [...value];
			if (newValues[index] !== '') {
				inputRefs.current[index - 1].focus();
				newValues[index - 1] = '';
				console.log(newValues);
			}
			newValues[index] = '';
			setValue(newValues.join(''));
		}
	};

	const inputs = Array(length).fill(0);

	return (
		<View style={{ flex: 1, backgroundColor: 'white' }}>
			<View>
				<Image
					source={require('../../assets/images/pin.png')}
					style={{ width: '100%', height: 200, resizeMode: 'contain' }}
				/>

				<Text
					style={{
						fontSize: 22,
						fontWeight: '600',
						color: '#000',
						paddingVertical: 5,
						alignSelf: 'center',
					}}
				>
					Enter Your 6 digit App Login Pin
				</Text>
			</View>

			{isError ? (
				<Animated.View
					style={[
						styles.errorText,
						{ transform: [{ translateX: shakeAnimation }] },
					]}
				>
					<View style={styles.container}>
						{inputs.map((_, index) => (
							<TextInput
								key={index}
								value={value[index] || ''}
								onChangeText={(text) => handleTextChange(text, index)}
								maxLength={1}
								style={[styles.input, isError && styles.inputError]}
								keyboardType="number-pad"
								ref={(ref) => {
									inputRefs.current[index] = ref;
								}}
								onSubmitEditing={() => {
									if (index === length - 1) {
										inputRefs.current[index].blur();
									}
								}}
								onKeyPress={(event) => handleKeyDown(event, index)}
							/>
						))}
					</View>
				</Animated.View>
			) : (
				<View style={styles.container}>
					{inputs.map((_, index) => (
						<TextInput
							key={index}
							value={value[index] || ''}
							onChangeText={(text) => handleTextChange(text, index)}
							maxLength={1}
							style={[styles.input, isError && styles.inputError]}
							keyboardType="numeric"
							ref={(ref) => {
								inputRefs.current[index] = ref;
							}}
							onSubmitEditing={() => {
								if (index === length - 1) {
									inputRefs.current[index].blur();
								}
							}}
							onKeyPress={(event) => handleKeyDown(event, index)}
						/>
					))}
				</View>
			)}

			{/* {isError && (
					<Animated.Text
						style={[
							styles.errorText,
							{ transform: [{ translateX: shakeAnimation }] },
						]}
					>
						Invalid pin code
					</Animated.Text>
				)} */}
		</View>
	);
};

const styles = StyleSheet.create({
	container: {
		flexDirection: 'row',
		justifyContent: 'space-between',
		width: '80%',
		alignSelf: 'center',
		marginTop: 20,
	},
	input: {
		height: 40,
		width: 40,
		borderWidth: 1,
		borderRadius: 5,
		textAlign: 'center',
		borderColor: 'lightgray',
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	inputError: {
		borderColor: 'red',
	},
	errorText: {
		color: 'red',
		fontSize: 16,
		marginTop: 8,
	},
});

export default UserPin;
