import React, { useState, useEffect } from 'react';
import { useNavigation } from '@react-navigation/native';
import Carousel from 'react-native-banner-carousel-updated';
import ServiceItemList from '../components/common/ServiceItemList';
import {
	View,
	Text,
	StyleSheet,
	FlatList,
	TouchableOpacity,
	Image,
	ScrollView,
	Dimensions,
} from 'react-native';
import ApiHandler from '../api/ApiHandler';
const images = [
	'http://superapi.kalyanicrm.com/media/store/images/10_KY3vlyo.jpg',
	'http://superapi.kalyanicrm.com/media/store/images/1_ZiZCPmy.jpg',
	'http://superapi.kalyanicrm.com/media/store/images/5_vZDybZv.jpg',
];

const BannerWidth = Dimensions.get('window').width - 20;
const BannerHeight = 200;

const ServiceDetails = ({ route, title }) => {
	const navigation = useNavigation();
	const [details, setDetails] = useState([]);
	const [show, setShow] = useState(false);
	const [currentIndex, setCurrentIndex] = useState(0);
	const [newarray, setNewArray] = useState([]);
	const [wishList, setWishList] = useState(false);
	const [deleteWishListId, setDeleteWishList] = useState({});
	useEffect(() => {
		getItemDetails(route.params.secid);
	}, []);

	const getItemDetails = (id) => {
		fetch(`http://superapi.kalyanicrm.com/showcases/get_itemdesc/${id}`)
			.then((response) => response.json())
			.then((json) => {
				setDetails(json);
				setShow(true);
				getDetails(route.params.activeId);
			});
	};

	const getDetails = (id) => {
		fetch(`http://superapi.kalyanicrm.com/showcases/get_dist_itemdesc/${id}`)
			.then((response) => response.json())
			.then((json) => {
				setShow(true);
				const filteredArray = json.filter((item) => {
					return item.id !== route.params.secid;
				});
				setNewArray(filteredArray);
			});
	};

	const renderPage = (image, index) => {
		return (
			<View key={index}>
				<Image
					style={{ width: BannerWidth, height: BannerHeight }}
					source={{ uri: image }}
				/>
			</View>
		);
	};
	const makeWishList = async (id) => {
		const response = await ApiHandler.makeWishList({ profile: 1, item: id });
		setDeleteWishList(response.data);
		setWishList(true);
	};
	const removeWishList = async (id) => {
		const response = await ApiHandler.removeWishList(id);
		setWishList(false);
	};

	useEffect(() => {
		const interval = setInterval(() => {
			setCurrentIndex(
				currentIndex === images.length - 1 ? 0 : currentIndex + 1
			);
		}, 3000);
		return () => clearInterval(interval);
	}, [currentIndex]);

	return (
		<>
			{show ? (
				<View
					style={{
						flex: 1,
						alignItems: 'center',
						backgroundColor: 'red',
					}}
				>
					<View
						style={{
							// width: '97%',
							// height: 300,
							borderRadius: 10,
							marginTop: 15,
							backgroundColor: 'white',
							shadowColor: 'gray',
							shadowOpacity: 0.26,
							shadowOffset: { width: 0, height: 2 },
							shadowRadius: 12,
							elevation: 3,
							// padding: 10,
							backgroundColor: '#fff',
						}}
					>
						<Carousel
							useNativeDriver={true}
							autoplay={true}
							autoplayTimeout={3000}
							loop
							showsPageIndicator={false}
							index={currentIndex}
							pageSize={BannerWidth}
							onPageChanged={(index) => setCurrentIndex(index)}
						>
							{images.map((image, index) => renderPage(image, index))}
						</Carousel>
					</View>

					<View
						style={{
							width: '97%',
							borderRadius: 5,
							marginTop: 5,
							backgroundColor: 'white',
							shadowColor: 'gray',
							shadowOpacity: 0.26,
							shadowOffset: { width: 0, height: 2 },
							shadowRadius: 12,
							elevation: 3,
							padding: 10,
						}}
					>
						<View style={{ padding: 10 }}>
							<Text style={{ fontSize: 15, fontWeight: '700', color: 'green' }}>
								Price Start From
							</Text>
							<Text style={{ fontSize: 25, fontWeight: '700' }}>Rs.5000/-</Text>
						</View>
						<View
							style={{
								flexDirection: 'row',
								justifyContent: 'space-around',
								marginTop: 5,
							}}
						>
							{wishList ? (
								<TouchableOpacity
									style={{
										backgroundColor: 'green',
										padding: 10,
										width: '49%',
										borderRadius: 10,
										textAlign: 'center',
									}}
									onPress={() => removeWishList(deleteWishListId.id)}
								>
									<Text
										style={{
											color: 'white',
											fontSize: 14,
											fontWeight: '600',
										}}
									>
										Wishlist Added
									</Text>
								</TouchableOpacity>
							) : (
								<TouchableOpacity
									style={{
										backgroundColor: '#F74300',
										padding: 10,
										width: '49%',
										borderRadius: 10,
										textAlign: 'center',
									}}
									onPress={() => makeWishList(details[0].id)}
								>
									<Text
										style={{
											color: 'white',
											fontSize: 14,
											fontWeight: '600',
										}}
									>
										Add Wishlist
									</Text>
								</TouchableOpacity>
							)}
							<TouchableOpacity
								style={{
									backgroundColor: '#F74300',
									padding: 10,
									width: '49%',
									borderRadius: 10,
									textAlign: 'center',
								}}
								onPress={() => navigation.navigate('ebookscreen', route.params)}
							>
								<Text
									style={{ color: 'white', fontSize: 14, fontWeight: '600' }}
								>
									Book Now
								</Text>
							</TouchableOpacity>
						</View>
						<View style={{ padding: 10 }}>
							<Text style={{ fontSize: 20, fontWeight: '700', color: 'black' }}>
								{details[0].title}
							</Text>
							<Text
								style={{
									marginTop: 5,
									fontSize: 13,
									fontWeight: 600,
									lineHeight: 20,
									color: 'rgb(103, 121, 135)',
								}}
							>
								{details[0].description}
							</Text>
						</View>
					</View>

					<View
						style={{
							width: '97%',
							borderRadius: 5,
							marginTop: 5,
							shadowColor: 'gray',
							shadowOpacity: 0.26,
							shadowOffset: { width: 0, height: 2 },
							shadowRadius: 12,
							elevation: 3,
						}}
					>
						{show ? (
							<View>
								<Text style={{ padding: 15, fontSize: 20, fontWeight: 700 }}>
									Related Products
								</Text>

								<FlatList
									showsHorizontalScrollIndicator={false}
									horizontal
									data={newarray}
									renderItem={({ item, index }) => {
										return (
											<View>
												<ServiceItemList
													data={newarray}
													index={index}
													item={item}
													activeId={route.params.activeId}
												/>
											</View>
										);
									}}
								/>
							</View>
						) : null}
					</View>
				</View>
			) : null}
		</>
	);
};
export default ServiceDetails;

