import {
	FlatList,
	Modal,
	StyleSheet,
	Text,
	TextInput,
	ToastAndroid,
	TouchableOpacity,
	View,
	ActivityIndicator,
} from 'react-native';
import React, { useState, useEffect } from 'react';
import {
	ArrowLeftIcon,
	MagnifyingGlassIcon,
	XMarkIcon,
} from 'react-native-heroicons/outline';

import { MicrophoneIcon } from 'react-native-heroicons/solid';
import AsyncStorage from '@react-native-async-storage/async-storage';

const placeholderText = [
	'Search "Insurance"',
	'Search "Car Services and Maintenance"',
	'Search for "Car Accessories"',
	'Search "Car Care Services"',
	'Search "Finance"',
	'Search "New Launches"',
	'Search "Used cars"',
	'Search "Brand New Cars"',
	'Search "MUVs"',
	'Search "SUVs"',
	'Search "Vans"',
	'Search "Hatchbacks"',
	'Search "Sedans"',
];

const SearchBar = ({ navigation }) => {
	const [currentPlaceholder, setCurrentPlaceholder] = useState(
		placeholderText[0]
	);
	let currentIndex = 0;

	useEffect(() => {
		const interval = setInterval(() => {
			setCurrentPlaceholder(placeholderText[currentIndex]);
			currentIndex = (currentIndex + 1) % placeholderText.length;
		}, 2000);
		return () => clearInterval(interval);
	}, []);

	return (
		<View style={styles.searchCont}>
			<TouchableOpacity
				style={styles.intoch}
				onPress={() => {
					navigation.navigate('searchCar');
				}}
			>
				<MagnifyingGlassIcon size={25} color="#0D4C92" />
				<Text
					placeholderTextColor={'grey'}
					placeholder='Searh  "Your Car"'
					style={styles.Textbar}
				>
					{currentPlaceholder}
				</Text>
			</TouchableOpacity>
			<View style={styles.micro}>
				<View style={styles.verticalBar} />
				<MicrophoneIcon size={20} color="#0D4C92" />
			</View>
		</View>
	);
};

export default SearchBar;

const styles = StyleSheet.create({
	searchCont: {
		width: '98%',
		alignSelf: 'center',
		borderRadius: 16,
		paddingHorizontal: 10,
		flexDirection: 'row',
		alignItems: 'center',
		justifyContent: 'space-between',
		borderColor: 'black',
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
		padding: 10,
		marginBottom: 10,
	},
	intoch: { flexDirection: 'row', alignItems: 'center' },
	micro: { flexDirection: 'row', alignItems: 'center' },
	verticalBar: {
		height: 30,
		borderWidth: 0.2,
		borderColor: 'black',
		marginHorizontal: 10,
	},
	Textbar: {
		paddingLeft: 7,
		outlineStyle: 'none',
		flex: 1,
	},
});

