import {
	FlatList,
	Image,
	StyleSheet,
	Text,
	TouchableOpacity,
	View,
} from 'react-native';
import React from 'react';
import { COLORS } from '../../Theme/Theme';
import { getComaPrice } from '../util/util';
const SmallCards = ({ data, navigation }) => {
	return (
		<>
			<FlatList
				data={data}
				keyExtractor={(item, index) => index}
				showsHorizontalScrollIndicator={false}
				horizontal
				renderItem={({ item }) => {
					return (
						<View style={styles.containerWrapper}>
							<TouchableOpacity
								underlayColor="orange"
								onPress={() =>
									navigation.navigate(item[0].page_navigation, {
										var_id: item[0].id,
										item_id: item[0].itemlist,
									})
								}
								style={styles.cardContainer}
							>
								<View style={{ width: 110, height: 100 }}>
									<Image source={item[0].images[0]} style={styles.img} />
								</View>
								<View style={styles.TextCont}>
									<Text style={styles.imgText}>{item[0].title}</Text>
									<Text style={styles.desc}>Ex-showroom Price /-</Text>
									<Text style={styles.offer}>
										{getComaPrice(item[0].price)} /-
									</Text>
								</View>
							</TouchableOpacity>
						</View>
					);
				}}
			/>
		</>
	);
};

export default SmallCards;

const styles = StyleSheet.create({
	cardContainer: {
		width: 300,
		height: 100,
		flexDirection: 'row',
		borderRadius: 12,
		backgroundColor: COLORS.WHITE,
		marginHorizontal: 5,
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
		// backgroundColor: 'red',
	},
	desc: { fontSize: 13, color: COLORS.FADED_TEXT, marginTop: 10 },
	img: {
		width: '100%',
		height: '100%',
		// borderTopLeftRadius: 10,
		// borderBottomLeftRadius: 10,
		resizeMode: 'contain',
		// backgroundColor: 'red',
	},
	imgText: { fontSize: 14, fontWeight: '600', color: '#000', paddingRight: 10 },
	// offer: { fontSize: 14, fontWeight: '600' },
	TextCont: {
		width: 180,
		// paddingVertical: 10,
		paddingLeft: 0,
		alignItems: 'flex-start',
		justifyContent: 'center',
	},
	containerWrapper: { paddingVertical: 10 },
});
