import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import React from 'react';
import { COLORS } from '../../Theme/Theme';
import {
	MagnifyingGlassIcon,
	MapPinIcon,
	UserCircleIcon,
	WalletIcon,
} from 'react-native-heroicons/solid';

import * as Linking from 'expo-linking';

const Header = ({ navigation }) => {
	return (
		<View style={styles.Header}>
			<MapPinIcon size={30} color="#0D4C92" />
			<View style={styles.addressCont}>
				<Text style={styles.headerText}>Home</Text>
				<Text style={styles.headerAddress}>
					KALYANI MOTORS PVT LTD, Pantara
				</Text>
			</View>
			<View style={styles.iconCont}>
				<TouchableOpacity>
					<WalletIcon size={30} color="#0D4C92" />
				</TouchableOpacity>
				<TouchableOpacity
					onPress={() => Linking.openURL('activity_login://LoginActivity')}
				>
					<UserCircleIcon size={30} color="#eee" />
				</TouchableOpacity>
			</View>
		</View>
	);
};

export default Header;

const styles = StyleSheet.create({
	addressCont: { flex: 1, paddingHorizontal: 5 },
	iconCont: {
		flexDirection: 'row',
		justifyContent: 'space-between',
		width: 70,
	},
	Header: {
		flexDirection: 'row',
		alignItems: 'center',
		justifyContent: 'flex-start',
		paddingBottom: 10,
	},
	headerAddress: { fontSize: 12, color: COLORS.FADED_TEXT },
	headerText: { fontSize: 16, fontWeight: '600', color: COLORS.TEXT },
});
