import {
	StyleSheet,
	Text,
	View,
	TouchableOpacity,
	Image,
	ToastAndroid,
	TextInput,
	FlatList,
	Alert,
	Animated,
} from 'react-native';
import React, { useState, useEffect, useRef } from 'react';
import { COLORS } from '../Theme/Theme';
import { BottomSheet } from 'react-native-btr';
import ApiHandler from '../api/ApiHandler';
import Loader from '../components/common/Loader';
import Toast from '../components/common/Toast';
import ScreenWrapper from '../components/common/ScreenWrapper';

const Login = ({ length = 6, onComplete, navigation }) => {
	const [showBottomSheet, setShowBottomSheet] = useState(false);
	const [phoneFormat, setPhoneFormat] = useState('');
	const [phone, setPhone] = useState('');
	const [showError, setShowError] = useState(false);
	const [verifyId, setVerifyId] = useState(false);
	const [isloading, setIsloading] = useState(true);
	const [isOtpSent, setIsOtpSent] = useState(false);
	const [OTP, setOTP] = useState('');
	const [newUser, setnewUser] = useState(true);
	const [wrongotp, setWrongOtp] = useState();

	const [value, setValue] = useState('');
	const [isError, setIsError] = useState(false);
	const [shakeAnimation] = useState(new Animated.Value(0));
	const inputRefs = useRef([]);

	const ValidateNumber = (phone) => {
		if (!/^[0-9]{10}$/i.test(phone)) {
			setShowError('Invalid Number');
		} else {
			checkUser(phone);
		}
	};

	const checkUser = async (phone) => {
		const response = await ApiHandler.checkUser({
			phone: phone,
		});
		console.log(response);
		if (response.data.error === 404) {
			_sendOtp(phone);
			setShowBottomSheet(1);
		} else {
			setnewUser(false);
			navigation.navigate('Enter Your Pin', {
				email: '',
				mobile: phone,
			});
		}
	};

	const onTextChange = (text) => {
		let phoneNumber = text;
		let newNum = phoneNumber.replace(/[\(\ \)\-]/g, '');
		setPhone(newNum);
		var cleaned = ('' + text).replace(/\D/g, '');
		var match = cleaned.match(/^(1|)?(\d{3})(\d{3})(\d{4})$/);
		if (match) {
			var intlCode = match[1] ? '+1 ' : '',
				number = [intlCode, '(', match[2], ') ', match[3], '-', match[4]].join(
					''
				);

			setPhoneFormat(number);

			return;
		}

		setPhoneFormat(text);
	};

	const _sendOtp = async (phone) => {
		let sendOtpPayload = {
			flow_id: '2079031d-a1ac-4bbe-bf58-2495bd6e5703',
			to: {
				mobile: `+91${phone}`,
				email: 'yadhurajm@gmail.com',
			},
		};
		const response = await ApiHandler.sendOtp(sendOtpPayload);
		if (!response.ok) return console.log(response.data);
		setVerifyId(response.data.data.verify_id);
		// console.log(response.data.data.verify_id);
		setIsOtpSent({ isOtpSent: true });
		// setIsloading({ isloading: false });
		// Open the OTP enter Page...
	};

	const _handleoptValid = async (verifyId, otp, phone) => {
		if (otp.length === 6) {
			let verifyData = {
				verify_id: verifyId,
				otp: otp,
			};
			console.log(phone);
			const response = await ApiHandler.verifyOtp(verifyData);
			if (response.ok) {
				setShowBottomSheet(null);
				setnewUser(true);
				navigation.navigate('conformPinScreen', {
					email: '',
					mobile: phone,
					userStatus: newUser,
				});
				setIsError(false);
			} else {
				setWrongOtp(true);
				setIsError(true);
			}
		}
	};

	useEffect(() => {
		if (isError) {
			Animated.sequence([
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: -10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 10,
					duration: 100,
					useNativeDriver: true,
				}),
				Animated.timing(shakeAnimation, {
					toValue: 0,
					duration: 100,
					useNativeDriver: true,
				}),
			]).start(() => setIsError(false));
		}
	}, [isError, shakeAnimation]);

	const handleTextChange = (text, index) => {
		const newValues = [...value];
		newValues[index] = text;
		setValue(newValues.join(''));

		if (text.length === 0 && index > 0) {
			inputRefs.current[index - 1].focus();
			newValues[index - 1] = '';
			setValue(newValues.join(''));
		} else if (text.length === 1 && index < length - 1) {
			inputRefs.current[index + 1].focus();
		}
		_handleoptValid(verifyId, newValues.join(''), phone);
	};

	const handleKeyDown = (event, index) => {
		if (event.nativeEvent.key === 'Backspace' && index > 0) {
			const newValues = [...value];
			if (newValues[index] !== '') {
				inputRefs.current[index - 1].focus();
				newValues[index - 1] = '';
				console.log(newValues);
			}
			newValues[index] = '';
			setValue(newValues.join(''));
		}
	};

	const inputs = Array(length).fill(0);

	return (
		<ScreenWrapper>
			<View style={{ backgroundColor: '#fff', flex: 1 }}>
				<View style={styles.imgCont}>
					<Image
						source={require('../../assets/images/loginBg.jpeg')}
						style={styles.img}
					/>
				</View>
				<View>
					<Text style={styles.headline}>
						Karnataka's #1{'\n'}Maruti Suzuki Dealership App.
					</Text>
				</View>
				<View style={styles.hrlines}>
					<View style={styles.line} />
					<Text style={styles.loginText}>Login or Sign up</Text>
					<View style={styles.line} />
				</View>
				<View
					style={{
						backgroundColor: '#eee',
						borderRadius: 12,
						width: '90%',
						alignSelf: 'center',
						alignItems: 'center',
						marginBottom: 25,
						elevation: 5,
						flexDirection: 'row',
						paddingHorizontal: 10,
					}}
				>
					<Image
						style={{ height: 25, width: 30, borderRadius: 4 }}
						source={require('../../assets/icon/index.png')}
					/>
					<Text
						style={{
							color: 'black',
							fontSize: 15,
							fontWeight: '500',
							paddingHorizontal: 4,
							marginLeft: 5,
						}}
					>
						+91
					</Text>
					<TextInput
						autoFocus={true}
						style={{
							width: 150,
							height: 50,
							outline: 'none',
							marginLeft: 10,
							fontSize: 18,
							flex: 1,
						}}
						placeholder="Enter your number"
						placeholderTextColor="grey"
						keyboardType="number-pad"
						maxLength={14}
						onChangeText={(e) => {
							onTextChange(e);
							setShowError('');
						}}
						value={phoneFormat}
					/>
					<Text style={{ color: 'red', fontSize: 12, paddingLeft: 10 }}>
						{showError}
					</Text>
				</View>

				<TouchableOpacity
					onPress={() => {
						// _checkExistingUser(phone);
						ValidateNumber(phone);
						// setShowBottomSheet(1);
						// navigation.navigate('conformPinScreen');
					}}
				>
					<View style={styles.btn}>
						<Text style={styles.txt}>continue</Text>
					</View>
				</TouchableOpacity>

				{/* <View style={styles.hrlines}>
				<View style={styles.sec_line} />
				<Text style={styles.loginText}>or</Text>
				<View style={styles.sec_line} />
			</View> */}

				{/* <View style={styles.socialMediaCont}>
				<View style={styles.logoCont}>
					<Image
						source={require('../../assets/images/google.png')}
						style={{ height: 25, width: 25 }}
					/>
				</View>
				<View style={styles.logoCont}>
					<Image
						source={require('../../assets/images/apple.png')}
						style={{ height: 25, width: 25 }}
					/>
				</View>
				<View style={styles.logoCont}>
					<Image
						source={require('../../assets/images/facebook.png')}
						style={{ height: 25, width: 25 }}
					/>
				</View>
				<View style={styles.logoCont}>
					<EllipsisHorizontalIcon size={20} color={'black'} />
				</View>
			</View> */}
				<Text
					style={{
						color: COLORS.FADED_TEXT,
						position: 'absolute',
						bottom: 10,
						textAlign: 'center',
						alignSelf: 'center',
					}}
				>
					By Continuing you agree our {'\n'} Terms and Condition content
					policies
				</Text>
			</View>

			{isloading ? (
				<>
					<BottomSheet
						visible={showBottomSheet === 1 ? true : false}
						onBackButtonPress={() => {
							setShowBottomSheet(null);
						}}
						onBackdropPress={() => {
							setShowBottomSheet(null);
						}}
					>
						<View style={styles.bottomNavigationView}>
							{/* Enter your pin  */}
							<Text style={{ fontSize: 12, fontWeight: '500', padding: 10 }}>
								Please Enter OTP sent to
								<Text
									style={{
										fontSize: 14,
										fontWeight: '600',
										color: COLORS.FADED_TEXT,
									}}
								>
									+91 {phone}
								</Text>
							</Text>
							<View
							// style={{
							// 	backgroundColor: '#eee',
							// 	borderRadius: 12,
							// 	width: '80%',
							// 	alignSelf: 'center',
							// 	alignItems: 'center',
							// 	marginBottom: 15,
							// 	elevation: 5,
							// 	flexDirection: 'row',
							// 	paddingHorizontal: 10,
							// }}
							>
								{/* <TextInput
									autoFocus={true}
									style={{ height: 50, outline: 'none' }}
									placeholder="Enter OTP"
									placeholderTextColor="grey"
									keyboardType="number-pad"
									maxLength={6}
									onChangeText={(e) => setOTP(e)}
								/> */}

								{isError ? (
									<Animated.View
										style={[
											styles.errorText,
											{ transform: [{ translateX: shakeAnimation }] },
										]}
									>
										<View style={styles.container}>
											{inputs.map((_, index) => (
												<TextInput
													key={index}
													value={value[index] || ''}
													onChangeText={(text) => handleTextChange(text, index)}
													maxLength={1}
													style={[styles.input, isError && styles.inputError]}
													keyboardType="number-pad"
													ref={(ref) => {
														inputRefs.current[index] = ref;
													}}
													onSubmitEditing={() => {
														if (index === length - 1) {
															inputRefs.current[index].blur();
														}
													}}
													onKeyPress={(event) => handleKeyDown(event, index)}
												/>
											))}
										</View>
									</Animated.View>
								) : (
									<View style={styles.container}>
										{inputs.map((_, index) => (
											<TextInput
												key={index}
												value={value[index] || ''}
												onChangeText={(text) => handleTextChange(text, index)}
												maxLength={1}
												style={[styles.input, isError && styles.inputError]}
												keyboardType="numeric"
												ref={(ref) => {
													inputRefs.current[index] = ref;
												}}
												onSubmitEditing={() => {
													if (index === length - 1) {
														inputRefs.current[index].blur();
													}
												}}
												onKeyPress={(event) => handleKeyDown(event, index)}
											/>
										))}
									</View>
								)}
							</View>

							{/* <TouchableOpacity
								style={{
									backgroundColor: 'orange',
									padding: 10,
									alignItems: 'center',
									justifyContent: 'center',
									borderRadius: 12,
									width: '40%',
									alignSelf: 'center',
								}}
								onPress={() => _handleoptValid(verifyId, OTP, phone, newUser)}
							>
								<Text>Submit</Text>
							</TouchableOpacity> */}
							{wrongotp ? (
								<Toast message={'warning'} toast={'Please Enter Correct OTP'} />
							) : null}
						</View>
					</BottomSheet>
				</>
			) : (
				<Loader />
			)}
		</ScreenWrapper>
	);
};

export default Login;

const styles = StyleSheet.create({
	btn: {
		backgroundColor: 'blue',
		padding: 15,
		borderRadius: 12,
		width: '90%',
		alignSelf: 'center',
		alignItems: 'center',
		justifyContent: 'center',
		marginBottom: 25,
	},
	headline: {
		fontSize: 25,
		fontWeight: '900',
		textAlign: 'center',
		color: COLORS.HEADER_TEXT,
		marginVertical: 20,
	},
	hrlines: {
		justifyContent: 'center',
		flexDirection: 'row',
		marginBottom: 20,
		alignItems: 'center',
		justifyContent: 'center',
	},
	img: { height: '100%', width: '100%', resizeMode: 'contain' },
	imgCont: { width: '100%', height: '25%' },
	line: { height: 1, flex: 1, backgroundColor: 'gray' },
	logoCont: {
		padding: 6,
		elevation: 6,
		backgroundColor: COLORS.WHITE,
		borderRadius: 12,
	},
	sec_line: { height: 0.8, width: '50%', backgroundColor: COLORS.FADED_TEXT },
	loginText: {
		marginLeft: 15,
		marginRight: 15,
		fontWeight: '600',
		fontSize: 17,
		color: COLORS.FADED_TEXT,
	},
	numBox: { width: '95%', height: 15, elevation: 6 },
	socialMediaCont: {
		alignItems: 'center',
		justifyContent: 'space-around',
		flexDirection: 'row',
		marginVertical: 10,
	},
	txt: { color: COLORS.WHITE, fontSize: 15, fontWeight: '500' },
	textInput: {},
	bottomNavigationView: {
		backgroundColor: '#fff',
		height: 300,
	},

	container: {
		flexDirection: 'row',
		justifyContent: 'space-between',
		width: '80%',
		alignSelf: 'center',
		marginTop: 20,
	},
	input: {
		height: 40,
		width: 40,
		borderWidth: 1,
		borderRadius: 5,
		textAlign: 'center',
		borderColor: 'lightgray',
		shadowColor: 'gray',
		shadowOpacity: 0.26,
		shadowOffset: { width: 0, height: 2 },
		shadowRadius: 10,
		elevation: 3,
	},
	inputError: {
		borderColor: 'red',
	},
	errorText: {
		color: 'red',
		fontSize: 16,
		marginTop: 8,
	},
});
