import { StyleSheet, Text, View } from 'react-native';
import React, { useState, useEffect } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import ScreenWrapper from './src/components/common/ScreenWrapper';
import AppNavigator from './src/navigation/AppNavigatior';
import AuthNavigatior from './src/navigation/AuthNavigatior';
import BottomNavigation from './src/navigation/BottomNavigation';
import Profile from './src/screens/Profile';
import * as Linking from 'expo-linking';
import Loader from './src/components/common/Loader';
import Details from './src/screens/Details';
import InsuranceScreen from './src/screens/InsuranceScreen';
import Insurance from './src/screens/Insurance';
import ApiHandler from './src/api/ApiHandler';
import AsyncStorage from '@react-native-async-storage/async-storage';

const prefix = Linking.createURL('/');

const App = () => {
	const [userDetails, setuserDetails] = useState({});
	const [validUser, setvalidUser] = useState(false);
	const [token, setToken] = useState('');

	useEffect(() => {
		setToken(window.location.href + '/Token_Attached');
		_getUserStorage();
	}, []);

	const linking = {
		prefixes: [prefix],
	};

	const _getUserStorage = async () => {
		try {
			const jsonValue = await AsyncStorage.getItem('userCredential');
			if (jsonValue !== null) {
				const myObject = JSON.parse(jsonValue);
				console.log(myObject);
				setuserDetails(myObject);
				_checkUserValid(myObject);
			}
		} catch (error) {
			console.log(error);
		}
	};

	const _checkUserValid = async ({ phone, password }) => {
		const response = await ApiHandler.loginUser({
			phone: phone,
			password: password,
		});
		if (!response.ok) return console.log('Something Went wrong...');
		setvalidUser(true);
	};

	
	const result = token.split('/');

	try {
		AsyncStorage.setItem(
			'userToken',
			JSON.stringify({
				token: result[result.length - 1],
			})
		);
	} catch (error) {
		console.log(error);
	}

	return (
		<NavigationContainer linking={linking}>
			{validUser ? <BottomNavigation /> : <AuthNavigatior />}
		</NavigationContainer>
	);
};

export default App;

const styles = StyleSheet.create({});
